import React, {useContext, useState} from "react";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { connect } from "react-redux";

import { userLogin } from '../../redux/user/user.actions';
import { setNewPinnedPost, updatePost, deletePost } from "../../redux/post/post.actions";


import { useSocket, socket } from "../../socket/socket";

import Modal from '../modal/Modal';


function timeDifference(current, previous) {

    var msPerMinute = 60 * 1000;
    var msPerHour = msPerMinute * 60;
    var msPerDay = msPerHour * 24;
    var msPerMonth = msPerDay * 30;
    var msPerYear = msPerDay * 365;

    var elapsed = current - previous;

    if (elapsed < msPerMinute) {
        if(elapsed / 1000 < 30) return "Just now"
         return Math.round(elapsed/1000) + ' seconds ago';   
    }

    else if (elapsed < msPerHour) {
         return Math.round(elapsed/msPerMinute) + ' minutes ago';   
    }

    else if (elapsed < msPerDay ) {
         return Math.round(elapsed/msPerHour ) + ' hours ago';   
    }

    else if (elapsed < msPerMonth) {
        return Math.round(elapsed/msPerDay) + ' days ago';   
    }

    else if (elapsed < msPerYear) {
        return Math.round(elapsed/msPerMonth) + ' months ago';   
    }

    else {
        return Math.round(elapsed/msPerYear ) + ' years ago';   
    }
}

function Post({ postData, largeFont, user, login, token, updatePost, deletePost, setPinnedPost }) {

    const { t } = useTranslation(["common"]);
    const navigate = useNavigate();
    useSocket();


    const [ value, setValue ] = useState("");
    var invalid = value.trim() === "" ? true : false; 
    
    const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);
    const [isPinModalOpen, setPinModalOpen] = useState(false);
    const [isUnpinModalOpen, setUnPinModalOpen] = useState(false);
    const [isReplyModalOpen, setReplyModalOpen] = useState(false);

    if(postData == null || postData == undefined) return alert("post object is null");
	largeFont = largeFont ? true : false;

    var isRetweet = postData.repostData !== undefined && postData.repostData !== null;
    var retweetedBy = isRetweet ? postData.postedBy.username : null;

    postData = isRetweet ? postData.repostData : postData;
    var postedBy = postData.postedBy;
    //console.log(postedBy)

    if(postedBy._id === undefined) {
        return console.log("User object not populated");
    }
    var displayName = postedBy.firstName + " " + postedBy.lastName;
    var timestamp = timeDifference(new Date(), new Date(postData.createdAt));
    
    var likedClass = postData.likes.includes(user._id) ? "active" : "";
    var retweetedClass = postData.repostUsers.includes(user._id) ? "active" : "";
	var largeFontClass = largeFont ? "largeFont" : "";	

	var retweetText = '';
	if(isRetweet){
		retweetText = (<span>Reposted by <a href={`/profile/${retweetedBy}`}>@{retweetedBy}</a></span>);
	}
	
	
	var replyFlag = "";
	if(postData.replyTo && postData.replyTo._id){
		if(!postData.replyTo._id){
			return alert("Reply to is not populated");
		}

		var replyToUsername = postData.replyTo.postedBy.username;

		replyFlag = (<div className='replyFlag'>
			Replying to <a href={`/profile/${replyToUsername}`}>@{replyToUsername} </a>
		</div>);
	}

    const handleChange = (event) => {
        const value = event.target.value;
        
            setValue(value);
      
    }
	
	const handleDelete = () => {
		setDeleteModalOpen(false)
       	async function putData(url = '') {
		    // Default options are marked with *
		    const response = await fetch(url, {
		    method: 'DELETE', // *GET, POST, PUT, DELETE, etc.
		    mode: 'cors', // no-cors, *cors, same-origin
		    headers: {
		        'Authorization': 'Bearer ' + token,
		        // 'Content-Type': 'application/x-www-form-urlencoded',
		    }
		    // body data type must match "Content-Type" header
		        });
		        
		        return response.json(); // parses JSON response into native JavaScript objects
		}

		putData(`http://localhost:5000/api/posts/${postData._id}`)
		.then(data => {
		    //console.log(data);
		    deletePost(postData);
		     // JSON data parsed by `data.json()` call
		}).catch(err => console.log(err));
    	}

	var button = "";
	var pinnedPostText = "";
	if(postData.postedBy._id === user._id){
		var pinnedClass = "";
		var dataTarget = "#confirmPinModal";
		if(postData.pinned === true){
			pinnedClass = "active";
			dataTarget = "#unpinModal";
			pinnedPostText = (<><i className='fas fa-thumbtack'></i><span>Pinned Post</span></>)
		}
		button = (<><button onClick={() => postData.pinned == true ? setUnPinModalOpen(true) : setPinModalOpen(true) } className={`pinButton ${pinnedClass}`} data-id={`${postData._id}`} data-toggle="modal" data-target={`${dataTarget}`}><i className="fas fa-thumbtack"></i></button>
		<button className="deleteButton" data-id={`${postData._id}`} onClick={() => setDeleteModalOpen(true)}><i className="fas fa-times"></i></button></>)
	}

    const handleLike = () => {
        
        async function putData(url = '', data = {}) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'PUT', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                // 'Content-Type': 'application/x-www-form-urlencoded',
            }
            // body data type must match "Content-Type" header
                });
                return response.json(); // parses JSON response into native JavaScript objects
        }

        putData(`http://localhost:5000/api/posts/${postData._id}/like`)
        .then(data => {
            //console.log(data);
            if(data.post._doc.postedBy._id != user._id) {
                socket.emit("notification recieved", data.post._doc.postedBy._id);
            }
            login(data.user._doc, data.token);
            updatePost(data.post._doc); // JSON data parsed by `data.json()` call
        }).catch(err => console.log(err));
        
      // console.log(id)
    }
   
	
    const handleRetweet = () => {
        async function putData(url = '', data = {}) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                // 'Content-Type': 'application/x-www-form-urlencoded',
            }
            // body data type must match "Content-Type" header
                });
                return response.json(); // parses JSON response into native JavaScript objects
        }

        putData(`http://localhost:5000/api/posts/${postData._id}/retweet`)
        .then(data => {
            //console.log(data);
            login(data.user._doc, data.token);
            updatePost(data.post._doc); // JSON data parsed by `data.json()` call
        }).catch(err => console.log(err));
    }

    const handlePin = () => {
        async function putData(url = '', data) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'PUT', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(data)// body data type must match "Content-Type" header
                });
            return response; // parses JSON response into native JavaScript objects
        }

        putData(`http://localhost:5000/api/posts/${postData._id}`, { pinned: true })
        .then(data => {
            setPinModalOpen(false)
            postData.pinned = true;
            setPinnedPost(postData); // JSON data parsed by `data.json()` call
        }).catch(err => console.log(err));
    }

    const handleUnpin = () => {
        async function putData(url = '', data) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'PUT', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(data)// body data type must match "Content-Type" header
                });
            return response; // parses JSON response into native JavaScript objects
        }

        putData(`http://localhost:5000/api/posts/${postData._id}`, { pinned: false })
        .then(data => {
            setUnPinModalOpen(false)
            postData.pinned = false;
            setPinnedPost(null); // JSON data parsed by `data.json()` call
        }).catch(err => console.log(err));
    }

    const handleReply = () => {
        setValue("");
        async function putData(url = '', data) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(data)// body data type must match "Content-Type" header
                });
            return response.json(); // parses JSON response into native JavaScript objects
        }

        putData(`http://localhost:5000/api/posts/`, { content:value, replyTo:postData._id})
        .then(data => {
            if(postData.postedBy._id != user._id) {
                socket.emit("notification recieved", postData.postedBy._id);
            }
            //console.log(data);
            navigate(`/post/${postData._id}`);
        }).catch(err => console.log(err));
    }

    const goToPostPage = (event) => {
        //console.log(event.target.localName);
         if(postData._id !== undefined && event.target.localName !== "button" && event.target.localName !== "a") {
             navigate('/post/' + postData._id);
         };
    }

    return (
    <>
    { postData.postedBy._id === user._id && isDeleteModalOpen && <Modal 
    	show={isDeleteModalOpen}
    	onCancel={() => setDeleteModalOpen(false)}
    	header={"Are you sure?"}
    	
    	footer={<><button onClick={() => setDeleteModalOpen(false)} className="btn btn-lg btn-primary">Close</button>
    		<button onClick={handleDelete} className="btn btn-lg btn-danger">Delete</button></>}
     > <h6>Once you delete there is no going back</h6></Modal>
     }

    { postData.postedBy._id === user._id && isPinModalOpen && <Modal 
            show={isPinModalOpen}
            onCancel={() => setPinModalOpen(false)}
            header={"Do you want to pin this post?"}
            
            footer={<><button onClick={() => setPinModalOpen(false)} className="btn btn-lg btn-danger">Close</button>
                <button onClick={handlePin} className="btn btn-lg btn-primary">Pin</button></>}
        > <h6>You can pin only one post.</h6></Modal>
    }
    { postData.pinned === true && postData.postedBy._id === user._id && isUnpinModalOpen && <Modal 
            show={isUnpinModalOpen}
            onCancel={() => setUnPinModalOpen(false)}
            header={"Do you want to Unpin this post?"}
            
            footer={<><button onClick={() => setUnPinModalOpen(false)} className="btn btn-lg btn-danger">Close</button>
                <button onClick={handleUnpin} className="btn btn-lg btn-primary">Unpin</button></>}
        > <h6>You want to Unpin this post?</h6></Modal>
    }

    { isReplyModalOpen && <Modal 
            show={isReplyModalOpen}
            onCancel={() => setReplyModalOpen(false)}
            header={"Reply"}
            
            footer={<><button onClick={() => setReplyModalOpen(false)} className="btn btn-lg btn-danger">Close</button>
                <button onClick={handleReply} className="btn btn-lg btn-primary" disabled={invalid}>{t("reply")}</button></>}
        > 
        <>
            <div className={`post ${largeFontClass}`} data-id={`${postData._id}`} onClick={goToPostPage}>
				<div className='postActionContainer'>{retweetText} </div>
                <div className='mainContentContainer'>
                    <div className='userImageContainer'>
                        <img src={`http://localhost:5000${postedBy.profilePic}`} />
                    </div>
                    <div className='postContentContainer'>
						<div className="pinnedPostText">{pinnedPostText}</div>
                        <div className='header'>
                            <Link className='displayName' to={`/profile/${postedBy.username}`}>{displayName}</Link>
                            <span className='username'>@{postedBy.username}</span>
                            <span className='date'>{timestamp}</span>                       
							{button}
                        </div>
						{replyFlag}
                        <div className='postBody'>
                            <span>{postData.content}</span>
                        </div>
                        <div className='postFooter'>
                            <div className='postButtonContainer'>
                                <button onClick={() => setReplyModalOpen(false)} data-toggle='modal' data-target='#replyModal'>
                                    <i className='far fa-comment'></i>
                                </button>
                            </div>
                            <div className='postButtonContainer green'>
                                <button onClick={handleRetweet} className={`retweetButton ${retweetedClass}`}>
                                    <i className='fas fa-retweet'></i>
                                    <span>{postData.repostUsers.length || ''}</span>
                                </button>
                            </div>
                            <div className='postButtonContainer red'>
                                <button onClick={handleLike} className={`likeButton ${likedClass}`}>
                                    <i className='far fa-heart'></i>
                                    <span>{postData.likes.length || ''}</span>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='postFormContainer'>
                <div className='userImageContainer'>
                    <img src={`http://localhost:5000${user.profilePic}`} alt="User's profile" />
                </div>   
                <div className='textareaContainer'>
                    <textarea id='postTextarea' placeholder={t("placeholder")} onChange={handleChange} value={value} />
                </div>   
            </div>
        </>
        
        </Modal>
    }

    
    <div className={`post ${largeFontClass}`} data-id={`${postData._id}`} onClick={goToPostPage}>
				<div className='postActionContainer'>{retweetText} </div>
                <div className='mainContentContainer'>
                    <div className='userImageContainer'>
                        <img src={`http://localhost:5000${postedBy.profilePic}`} />
                    </div>
                    <div className='postContentContainer'>
						<div className="pinnedPostText">{pinnedPostText}</div>
                        <div className='header'>
                            <Link className='displayName' to={`/profile/${postedBy.username}`}>{displayName}</Link>
                            <span className='username'>@{postedBy.username}</span>
                            <span className='date'>{timestamp}</span>                       
							{button}
                        </div>
						{replyFlag}
                        <div className='postBody'>
                            <span>{postData.content}</span>
                        </div>
                        <div className='postFooter'>
                            <div className='postButtonContainer'>
                                <button onClick={() => setReplyModalOpen(true)} data-toggle='modal' data-target='#replyModal'>
                                    <i className='far fa-comment'></i>
                                </button>
                            </div>
                            <div className='postButtonContainer green'>
                                <button onClick={handleRetweet} className={`retweetButton ${retweetedClass}`}>
                                    <i className='fas fa-retweet'></i>
                                    <span>{postData.repostUsers.length || ''}</span>
                                </button>
                            </div>
                            <div className='postButtonContainer red'>
                                <button onClick={handleLike} className={`likeButton ${likedClass}`}>
                                    <i className='far fa-heart'></i>
                                    <span>{postData.likes.length || ''}</span>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> </>
            )
}


const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token
})
  
const mapDispatchToProps = dispatch => ({
    login: (user, token) => dispatch(userLogin(user, token)),
    setPinnedPost: (post) => dispatch(setNewPinnedPost(post)),
    updatePost: (post) => dispatch(updatePost(post)),
    deletePost: (post) => dispatch(deletePost(post))
})
  
export default connect(mapStateToProps, mapDispatchToProps)(Post);
