import React from "react";
import { connect } from "react-redux";
import { AiOutlineClose } from 'react-icons/ai';

import { oddTypeChange } from '../../utils/oddsTypeChange';

import { deleteBet, updateBetStake, calculateTotalReturn, setBetType, setErrorMessage, setErrorValue } from "../../redux/betslip/betslip.actions";


const Bet = ({ title, odds, category, teams, id, stake, stakeType, isMybet, bets, betType, oddType, setErrorMessage, setBetType, deleteBet, updateBetStake, calculateTotalReturn, setErrorValue }) => {

    const handleStakeChange = (e) => {
            updateBetStake(id, e.target.value);
            calculateTotalReturn();
            //console.log(bets);
    }

    const handleDelete = () => {
            if(stakeType == 'system'){
                
                setBetType('multi');
            }
            setErrorValue(null);
            setErrorMessage(null);
            deleteBet(id)
            calculateTotalReturn();
            //console.log(bets);
    }

    var toReturn;

    if(stakeType == 'single' || stakeType == 'multi'){
        toReturn = odds * stake;
    } else {
        toReturn = 0;
    }

    return (
        <>
        <div className="betslip__header">
            <div className='betslip__header__text'>
                {!isMybet && <AiOutlineClose fontSize={"1.5rem"} className="betslip__header__icon" onClick={handleDelete} />}
                <h1 className="betslip__title">
                    {title}
                </h1>
                <h1 className="betslip__odd">
                    {odds == 'suspended' ? <span>SUS</span> : odds <= 1 ? odds : oddTypeChange(odds, oddType)}
                </h1>
            </div>

            {isMybet 
            ? 
            <input type='number' className='betslip__stake__input' placeholder='Stake' value={stake} readOnly />
            :
            <div className="betslip__header__stake">
                {(stakeType == 'single' && bets.length > 1 && odds != 'suspended') &&  <div className="betslip__stake">
                    <input type='number' className='betslip__stake__input' placeholder='Stake' value={stake > 0 && stake} onChange={handleStakeChange} />
                    <span className="betslip__stake__toreturn">{`To Return: ${toReturn > 0 ? parseFloat(toReturn).toFixed(2) : 0}`}</span>
                </div>}
                
            </div>}

            
        </div>
        <div className="betslip__body">
            <p>{category}</p>
            <p>{teams}</p>
        </div>
        
    </> 
    )
}

const mapStateToProps = state => ({
    bets: state.betslip.bets,
    oddType: state.oddType.oddType,
    betType: state.betslip.betType
})

const mapDispatchToProps = dispatch => ({
    deleteBet: id => dispatch(deleteBet(id)),
    updateBetStake: (id, stake) => dispatch(updateBetStake(id, stake)),
    setBetType: (type) => dispatch(setBetType(type)),
    setErrorMessage: (value) => dispatch(setErrorMessage(value)),
    setErrorValue: (value) => dispatch(setErrorValue(value)),
    calculateTotalReturn: () => dispatch(calculateTotalReturn())
})

export default connect(mapStateToProps, mapDispatchToProps)(Bet);