import * as React from "react";
import { useParams } from "react-router-dom";
import { connect } from "react-redux";

import { setPageType } from '../../redux/page/page.actions';

import MainLayout from '../../layouts/main-layout.component';

import Post from "../../components/post/post.component";


function PostPage({ user, token, setPageType }) {

    const [results, setResults] = React.useState(null);

    const id = useParams().id;

    React.useLayoutEffect(() => {
        setPageType('social');
      },[]);

    React.useEffect(() => {
        if(user && id){
            var url;
         	url = `http://localhost:5000/api/posts/${id}`;
            
            async function getData(url = '') {
                // Default options are marked with *
                const response = await fetch(url, {
                method: 'GET', // *GET, POST, PUT, DELETE, etc.
                mode: 'cors', // no-cors, *cors, same-origin
                headers: {
                    'Authorization': 'Bearer ' + token,
                    // 'Content-Type': 'application/x-www-form-urlencoded',
                }
                // body data type must match "Content-Type" header
                    });
                    return response.json(); // parses JSON response into native JavaScript objects
            }
      
            getData(url)
            .then(data => {
                //console.log(data);
                setResults(data); // JSON data parsed by `data.json()` call
            }).catch(err => console.log(err));
        }

    },[token,user,id])
  return (
      	<MainLayout>
            {
                results 
                &&
                <div className="resultsContainer">
                    {
                        results.replyTo !== undefined && results.replyTo !== null  && results.replyTo._id !== undefined 
                        &&
                        <Post postData={results.replyTo} />
                    }

                    <Post postData={results.postData} largeFont={true} />

                    {
                        results.replies.map(result => {
                        return <Post key={result._id} postData={result} />
                        })
                    }
                </div> 
            }
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token
})

const mapDispatchToProps = dispatch => ({
    setPageType: (pageType) => dispatch(setPageType(pageType))
})
  
  
export default connect(mapStateToProps, mapDispatchToProps)(PostPage);
  