import * as React from "react";
import { connect } from "react-redux";
import { useTranslation } from 'react-i18next';

import { setPageType } from '../../redux/page/page.actions';
import { setUpdateBet, calculateTotalReturn, setBetType, setTotalStake, updateBetStake, setErrorMessage, setUseBonus, setErrorValue } from '../../redux/betslip/betslip.actions';


import MainLayout from '../../layouts/main-layout.component';
import Bet from '../../components/bet/bet.component';
import MyBetContainer from "../../components/my-bet/MyBet";

import '../../components/betslip/BetSlip.css';

function MyBetPage(props) {

    const { t } = useTranslation(["common"]);

    const { token, 
            setPageType,
            bets, 
            updateBet, 
            betType, 
            totalStake,
            errorMessage,
            errorValue, 
            singleTotalReturn, 
            multiTotalReturn, 
            systemTotalReturn, 
            setErrorMessage,
            setErrorValue,
            setUpdateBet, 
            setBetType, 
            setBetStake, 
            setTotalStake, 
            calculateTotalReturn,
            useBonus,
            setUseBonus,
     } = props;



  const [tab, setTab] = React.useState('mybets');
  const [mybets, setMyBets] = React.useState(null);
  const [history, setHistory] = React.useState(null);
  const [betsInfo, setBetsInfo] = React.useState([]);
  const [cashout, setCashout] = React.useState(null);

  const handleTabChange = () =>{
    tab === 'mybets' ? setTab('history') : setTab('mybets');
  }


  React.useLayoutEffect(() => {
    setPageType('sports');
  },[]);

  React.useEffect(() => {

        setMyBets(null);
        setHistory(null);
        var url = tab === 'mybets' ? `http://localhost:5000/api/bets/my-bets` : `http://localhost:5000/api/bets/history-bets`;
        // Example POST method implementation:
        async function putData(url = '') {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'GET', // *GET, POST, PUT, DELETE, etc. // include, *same-origin, omit
            headers: {
            'Authorization': 'Bearer ' + token
            // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors'
        });
        return response.json(); // parses JSON response into native JavaScript objects
        }
        
        putData(url)
        .then(data => {
            //console.log(data);
            tab === 'mybets' ? setMyBets([...data.data.reverse()]) : setHistory([...data.data.reverse()]);
            setCashout(data.cashoutPercentage);
            //textbox.current.focus();
            //login(data.user._doc, data.token);	
        }).catch(err => console.log(err));
  },[tab]);

  React.useEffect(() => {
    if(mybets || history){
        setBetsInfo([]);
        const storedBetslips = mybets ? mybets : history;
        storedBetslips.forEach(slip => {
            setBetsInfo(prev => [...prev, ...slip.bets]);
        });
        //console.log(betsInfo);
    }
    
  },[mybets, history])

  return (
      	<MainLayout>
            <div className="tabsContainer">
                <button onClick={handleTabChange} className={`tab ${tab === 'mybets' ? 'active' : ''}`}><span>My Bets</span></button>
                <button onClick={handleTabChange} className={`tab ${tab === 'history' ? 'active' : ''}`}><span>Bet History</span></button>
            </div>
            <div className="resultsContainer">
                {/* {
                    tab === 'betslip' && history && history.map(bet => {
                        return <Bet 
                                  key={bet._id} 
                                  id={bet._id} 
                                  title={bet.optionName} 
                                  odds={bet.odds} category={bet.category} 
                                  teams={bet.teams}
                                  stake={bet.stake} 
                                  stakeType={"single"} 
                                  isMybet={true} 
                              />
                      })
                } */}
                {
                   tab === 'history' && betsInfo && <p>My History</p>
                }
                {
                    tab === 'mybets' && betsInfo && <MyBetContainer 
                        title={"Currently Ongoing Bets"} 
                        bets={betsInfo}
                        cashoutPercentage={cashout}
                    />
                }
            </div>
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
  token: state.user.token,
  bets: state.betslip.bets,
  updateBet: state.betslip.updateBet, 
  betType: state.betslip.betType, 
  totalStake: state.betslip.totalStake,
  errorMessage: state.betslip.errorMessage,
  errorValue: state.betslip.errorValue, 
  singleTotalReturn:state.betslip.singleTotalReturn, 
  multiTotalReturn: state.betslip.multiTotalReturn, 
  systemTotalReturn: state.betslip.systemTotalReturn, 
  useBonus: state.betslip.useBonus
})

const mapDispatchToProps = dispatch => ({
    setPageType: (pageType) => dispatch(setPageType(pageType)),
    setUpdateBet: value => dispatch(setUpdateBet(value)),
    setBetType: value => dispatch(setBetType(value)),
    setTotalStake: value => dispatch(setTotalStake(value)),
    setBetStake: (id, value) => dispatch(updateBetStake(id, value)),
    setErrorMessage:(value) => dispatch(setErrorMessage(value)),
    setErrorValue:(value) => dispatch(setErrorValue(value)),
    calculateTotalReturn: () => dispatch(calculateTotalReturn()),
    setUseBonus: (value) => dispatch(setUseBonus(value))
})


export default connect(mapStateToProps, mapDispatchToProps)(MyBetPage);