import React from "react";
import { connect } from "react-redux";
import { useTranslation } from 'react-i18next';

import { userLogin } from "../../redux/user/user.actions";


import './MyBet.css';

const MultiBetContainer = ({ headerBet, children }) => {
    const [collapsed, setCollapsed] = React.useState(false);
 
    const handleClick = (e) => {
        setCollapsed(!collapsed);
    }

    return <div className="multibetcontainer" onClick={handleClick}>
        {headerBet}
        <div className={`multibetcontaier__collapsed ${collapsed ? 'active' : ''}`}>
            {children}
        </div>
    </div>
}

const MyBetBody = ({gameId, categoryId, optionId, optionName, date, time, stake, toReturn, odds, betType, games, pregames, precricketgames,  cashoutPercentage, token, bet, totalReturn, login}) => {
    const [currentOdds, setCurrentOdds] = React.useState(null);
    const [cashOutAmount, setCashOutAmount] = React.useState(null);
    const [isSuccessful, SetIsSuccessful] = React.useState(false);
    const [btnText, setBtnText] = React.useState('Cashout');
    const [errorMessage, setErrorMessage] = React.useState(null);
    
    const { t } = useTranslation(["common"]);

    React.useEffect(() => {
        if(games && pregames && precricketgames){

            const [foundGame, gameType] = getGame(gameId);
            if(gameType != "precricket"){
            if(foundGame){
                    //check if the game has optionMarkets
                    if(foundGame.optionMarkets[0]){
                        //find which category the bet belongs
                        var foundCategoryIndex = foundGame.optionMarkets.findIndex(option => option.id == categoryId);
                        //check if the category exists and visible
                        if(foundCategoryIndex < 0 || foundGame.optionMarkets[foundCategoryIndex].status != 'Visible'){
                        //if not found and not visible set odds suspended 
                        setCurrentOdds('Suspended');
                        } else{
                        // else if the category is found and visible check if the optionId matches with bet
                        foundGame.optionMarkets[foundCategoryIndex].options.forEach(option => {
                            
                            if(option.id == optionId){
                            //if so update the odds of the bet
                            setCurrentOdds(option.price.odds)
                            }
                        })
                        }
                    } else if(foundGame.Markets[0]){ //check if the game has Markets
                        //find which category the bet belongs
                        var foundCategoryIndex = foundGame.Markets.findIndex(option => option.id == categoryId);
                        //check if the category exists and visible
                        if(foundCategoryIndex < 0 || foundGame.Markets[foundCategoryIndex].visibility != 'Visible'){
                        //if not found and not visible set odds suspended 
                        setCurrentOdds('Suspended')
                        } else{
                        // else if the category is found and visible check if the optionId matches with bet
                        foundGame.Markets[foundCategoryIndex].results.forEach(option => {
                            if(option.id == optionId){
                            //if so update the odds of the bet
                            setCurrentOdds(option.odds)
                            }
                        })
                        }
                    } else{
                        // if the game doesn't have optionMarket & Market set the odds to suspended
                        setCurrentOdds('Suspended');
                    }
                } else{
                    setCurrentOdds('Ended')
                }
            } else{
                if(foundGame){
                    //check if the game has optionMarkets
                    if(foundGame.markets[0]){
                    //find which category the bet belongs
                    var foundCategoryIndex = foundGame.markets.findIndex(option => option.market.marketId == bet.categoryId);
                    //check if the category exists and visible
                    if(foundCategoryIndex < 0 || foundGame.markets[foundCategoryIndex].marketStatus != 'OPEN'){
                        //if not found and not visible set odds suspended 
                        setCurrentOdds('Suspended');
                    } else{
                        // else if the category is found and visible check if the optionId matches with bet
                        foundGame.markets[foundCategoryIndex].runnerDetails.forEach(option => {
                        
                        if(option.selectionId == bet.optionId){
                            //if so update the odds of the bet
                            setCurrentOdds(option.runnerOdds.decimalDisplayOdds.decimalOdds)
                        }
                        })
                    }
                    } else{
                        // if the game doesn't have optionMarket & Market set the odds to suspended
                        setCurrentOdds('Suspended');
                    }
                }  else{
                    // if the game doesn't have optionMarket & Market set the odds to suspended
                    setCurrentOdds('Ended')
                }
            }




            // const foundGame = games.find(game => game.Id == gameId); 
            // if(foundGame){
            //     //check if the game has optionMarkets
            //     if(foundGame.optionMarkets[0]){
            //         //find which category the bet belongs
            //         var foundCategoryIndex = foundGame.optionMarkets.findIndex(option => option.id == categoryId);
            //         //check if the category exists and visible
            //         if(foundCategoryIndex < 0 || foundGame.optionMarkets[foundCategoryIndex].status != 'Visible'){
            //         //if not found and not visible set odds suspended 
            //         setCurrentOdds('Suspended');
            //         } else{
            //         // else if the category is found and visible check if the optionId matches with bet
            //         foundGame.optionMarkets[foundCategoryIndex].options.forEach(option => {
                        
            //             if(option.id == optionId){
            //             //if so update the odds of the bet
            //             setCurrentOdds(option.price.odds)
            //             }
            //         })
            //         }
            //     } else if(foundGame.Markets[0]){ //check if the game has Markets
            //         //find which category the bet belongs
            //         var foundCategoryIndex = foundGame.Markets.findIndex(option => option.id == categoryId);
            //         //check if the category exists and visible
            //         if(foundCategoryIndex < 0 || foundGame.Markets[foundCategoryIndex].visibility != 'Visible'){
            //         //if not found and not visible set odds suspended 
            //         setCurrentOdds('Suspended')
            //         } else{
            //         // else if the category is found and visible check if the optionId matches with bet
            //         foundGame.Markets[foundCategoryIndex].results.forEach(option => {
            //             if(option.id == optionId){
            //             //if so update the odds of the bet
            //             setCurrentOdds(option.odds)
            //             }
            //         })
            //         }
            //     } else{
            //         // if the game doesn't have optionMarket & Market set the odds to suspended
            //         setCurrentOdds('Suspended');
            //     }
            // } else{
            //     setCurrentOdds('Ended')
            // }
        }
        
    },[games, pregames, precricketgames]);

    React.useEffect(() => {
        if(currentOdds && currentOdds != 'Ended'){
            var xr = totalReturn / currentOdds;
            var yr = (xr * cashoutPercentage) / 100
            setCashOutAmount(parseFloat(xr - yr).toFixed(2))
        }
    }, [currentOdds]);

    const getGame = (gameId) => {
        if (games != null) {
          var foundGame = games.find(game => game.Id == gameId);
          if(foundGame != null){
              return [foundGame, 'game'];
          } else{
              if(pregames != null){
                  foundGame = pregames.find(game => game.Id == gameId);
                  if(foundGame != null){
                    return [foundGame, 'pregame'];
                  } else{
                    if(precricketgames != null){
                        foundGame = precricketgames.find(game => game.event.eventId == gameId);
                        //console.log()
                        if(foundGame != null){
                          return [foundGame, 'precricket'];
                        } else{
                            return [null, null];
                        }
                    } else{
                        return [null, null];
                    }
                  }
              } else{
                  return [null, null];
              }
          }
        } else {
          return [null, null];
        }
    }

    const handleStatusUpdate = () => {
        if(!isSuccessful){        
            var url = `http://localhost:5000/api/bets/cashout`;
            // Example POST method implementation:
            async function putData(url = '', data) {
                // Default options are marked with *
                const response = await fetch(url, {
                method: 'PUT', // *GET, POST, PUT, DELETE, etc. // include, *same-origin, omit
                headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
                },
                mode: 'cors',
                body: JSON.stringify(data)
            });
            return response.json(); // parses JSON response into native JavaScript objects
            }
            setErrorMessage(null);
            SetIsSuccessful(true);
            setBtnText('Cashing')
            putData(url, {
                bet:bet,
                currentOdds: currentOdds,
                cashoutPercentage:cashoutPercentage,
                cashOutAmount:cashOutAmount
            })
            .then(data => {
                console.log(data);
                if(data.errorMessage){
                    setErrorMessage(data.errorMessage);
                    SetIsSuccessful(false);
                } else{
                    SetIsSuccessful(true);
                    setBtnText('Won')
                    login(data.user, data.token);
                }
            }).catch(err => console.log(err));
        }
    }

    return <>
    {errorMessage && <p className='betslip__errorMessage'>{t(errorMessage)}</p>}
    <div className={`mybet__body ${betType}`}>
        <div className="mybet__body__details">
            <p className="mybet__body__details__option">{optionName}@ {odds}</p>
            <p className="mybet__body__details__stake">{stake} {betType}</p>
        </div>
        <div className="mybet__body__date">
            <span>{date}</span> 
            <span>{time}</span> 
        </div>
        <div className="mybet__body__odds">
            {currentOdds ? currentOdds : 'N/A'}
            {currentOdds && currentOdds != 'Ended' && currentOdds!= 'N/A' && currentOdds < 10
                && betType == 'single' && <button className="mybet__body__odds__btn" onClick={handleStatusUpdate}>
                    {btnText} {cashOutAmount}
                </button>
            }
        </div>
        <div className="mybet__body__stake">
            {stake}
        </div>
        <div className="mybet__body__return">
            {toReturn}
        </div>
        
    </div></>
}

//Currently Ongoing Bets
const MyBetContainer = ({title, bets, cashoutPercentage, games, pregames, precricketgames, token, login}) => {
    //console.log(cashoutPercentage);
    const [totalStake, setTotalStake] = React.useState(null);
    const [totalReturn, setTotalReturn] = React.useState(null);
    const [formattedData, setFormattedData] = React.useState({});

    React.useEffect(() => {
        var copyGames = {}
        bets.forEach(bet => {
            copyGames[bet.commonId] ? 
                copyGames[bet.commonId] = [...copyGames[bet.commonId], bet ] 
                : 
                copyGames[bet.commonId] = [bet];
        })
        setFormattedData(copyGames);
    }, [bets]);

    // React.useEffect(() => {
    //     console.log(formattedData);
    // },[formattedData]);

    React.useEffect(() => {
        var totalStake = 0;
        var totalReturn = 0;
        bets.forEach((bet) => {
            totalStake += bet.totalStake;
            totalReturn += bet.netReturn
        })
        //console.log(bets)
        setTotalStake(totalStake);
        setTotalReturn(totalReturn);
    },[bets]);
    return <div className="mybet__container">
        <h1 className="mybet__heading">
            {title}
        </h1>
        <div className="mybet__header ">
            <div className="mybet__header__details">
                Bet Details
            </div>
            <div className="mybet__header__date">
                Date/Time
            </div>
            <div className="mybet__header__odds">
                Live Odds
            </div>
            <div className="mybet__header__stake">
                Stake
            </div>
            <div className="mybet__header__return">
                Net Return
            </div>
            
        </div>

        {Object.keys(formattedData).length > 0 &&
            Object.keys(formattedData).map(key => (
                formattedData[key].length > 1 
                ?
                     <MultiBetContainer key={formattedData[key][0]._id} headerBet={
                        <MyBetBody
                            gameId={formattedData[key][0].gameId} 
                            categoryId={formattedData[key][0].categoryId} 
                            optionId={formattedData[key][0].optionId} 
                            optionName={formattedData[key][0].optionName}
                            date={new Date(formattedData[key][0].createdAt).toLocaleDateString()} 
                            time={new Date(formattedData[key][0].createdAt).toLocaleTimeString()} 
                            stake={formattedData[key][0].totalStake} 
                            toReturn={formattedData[key][0].netReturn.toFixed(2)} 
                            odds={formattedData[key][0].odds}
                            betType={formattedData[key][0].betType}
                            games={games ? games : null}
                            pregames={pregames ? pregames: null}
                            precricketgames={precricketgames ? precricketgames : null}
                            cashoutPercentage={cashoutPercentage}
                            token={token}
                            bet={formattedData[key][0]}
                            totalReturn={formattedData[key][0].totalReturn}
                            login={login}
                        />
                    }>
                         {
                            formattedData[key].slice(1).map(bet => {
                                return <MyBetBody
                                    key={bet._id}
                                    gameId={bet.gameId} 
                                    categoryId={bet.categoryId} 
                                    optionId={bet.optionId} 
                                    optionName={bet.optionName}
                                    date={new Date(bet.createdAt).toLocaleDateString()} 
                                    time={new Date(bet.createdAt).toLocaleTimeString()} 
                                    stake={bet.totalStake} 
                                    toReturn={bet.netReturn.toFixed(2)} 
                                    odds={bet.odds}
                                    betType={bet.betType}
                                    games={games ? games : null}
                                    pregames={pregames ? pregames: null}
                                    precricketgames={precricketgames ? precricketgames : null}
                                    cashoutPercentage={cashoutPercentage}
                                    token={token}
                                    bet={bet}
                                    totalReturn={bet.totalReturn}
                                    login={login}
                                />
                            })
                        }

                    </MultiBetContainer>
                :
                (
                    formattedData[key].map(bet => (
                        <MyBetBody
                            key={bet._id}
                            gameId={bet.gameId} 
                            categoryId={bet.categoryId} 
                            optionId={bet.optionId} 
                            optionName={bet.optionName}
                            date={new Date(bet.createdAt).toLocaleDateString()} 
                            time={new Date(bet.createdAt).toLocaleTimeString()} 
                            stake={bet.totalStake} 
                            toReturn={bet.netReturn.toFixed(2)} 
                            odds={bet.odds}
                            betType={bet.betType}
                            games={games ? games : null}
                            pregames={pregames ? pregames: null}
                            precricketgames={precricketgames ? precricketgames : null}
                            cashoutPercentage={cashoutPercentage}
                            token={token}
                            bet={bet}
                            totalReturn={bet.totalReturn}
                            login={login}
                        />
                    ))
                )
            ))
        }

        <div className="mybet__footer">
            <div className="mybet__footer__details">
                Total for this period 
            </div>
            <div className="mybet__footer__return">
                {totalStake ? totalStake : 'N/A'}
            </div>
            <div className="mybet__footer__odds">
                {totalReturn ? totalReturn.toFixed(2) : 'N/A'}
            </div>
        </div>
    </div>
}

const mapStateToProps = state => ({
    games: state.game.games,
    pregames: state.pregame.games,
    precricketgames:state.pregame.cricketData,
    token: state.user.token
})

const mapDispatchToProps = dispatch => ({
    login: (user,token) => dispatch(userLogin(user,token))
})

export default connect(mapStateToProps, mapDispatchToProps)(MyBetContainer);