import ActionTypes from './oddType.types';

const INITIAL_STATE = {
    oddType: 'decimal',
}

export const oddTypeReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ActionTypes.SET_ODD_TYPE:
          return {
                oddType: action.oddType
            };
          break;
        default:
            return state;
      }
}