import ActionTypes from './user.types';

const INITIAL_STATE = {
    isLoggedIn: false,
    user: null,
    token: null,
}

export const userReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ActionTypes.USER_LOGIN:
          return {
                isLoggedIn: true,
                user: action.user,
                token: action.token,
            };
          break;
        case ActionTypes.USER_LOGOUT:
            return {
                isLoggedIn: false,
                user: null,
                token: null,
            };
          break;
        default:
            return state;
      }
}