import { useEffect, Suspense } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import axios from 'axios';
import { connect } from 'react-redux';


import Register from './pages/register/register.component';
import Login from './pages/login/login.component';
import HomePage from './pages/home/home.component';
import Profile from './pages/profile/profile.component';
import Inbox from './pages/inbox/inbox.component';
import ChatPage from "./pages/chat/chat.component";
import SearchPage from './pages/search/searchPage.component';
import NotificationPage from './pages/notification/notification.component';
import PostPage from './pages/post/post.component';
import NewMessagePage from './pages/newMessage/newMessage.component';
import SportsPage from './pages/sports/sportsPage.component';
import PreSportsPage from "./pages/presports/presports";
import GamePage from './pages/game/gamePage.component';
import FollowersAndFollowingPage from './pages/followersAndFollowing/followersAndFollowing.component';
import MyBet from "./pages/mybet/MyBet";
import QuizOverview from "./pages/quizoverview/QuizOverview";


import { setNewGames } from "./redux/game/game.actions";
import { setPreGames, setCricketData } from "./redux/pregame/pregame.actions";
import { putNotifications } from "./redux/notification/notification.actions";
import { updateBetOdds, deleteBet, calculateTotalReturn, setErrorMessage } from './redux/betslip/betslip.actions'

import { useSocket, isSocketConnected, socket } from './socket/socket';

function App({ user, token, games, pregames, precricketgames, bets, updateBet, setGames, setPreGames, setCricketData, deleteBet, updateBetOdds, calculateTotalReturn, betErrorMessage }) {


  useSocket();

  const getData = async () => {
    const res = await axios.get('https://geolocation-db.com/json/')
    console.log(res.data);
    //alert(res.data)
  }

  // useEffect(() => {
  //   if (user && isSocketConnected) {
  //     getData();
  //     socket.emit('get game data');
  //     console.log("req for data sent")
  //     socket.off("get game data").on("get game data", (data) => {
  //       if (data != null) {
  //         console.log("Game came");
  //         setGames(data);
  //       } else {
  //         console.log("came but null");
  //       }
  //     })
  //   }
  // }, [user, token, isSocketConnected])

  useEffect(() => {
    if (user && isSocketConnected) {
      socket.emit('get precricket data');
      
      console.log("cricket data")
      //return;
      socket.off("get precricket data").on("get precricket data", (data) => {
        if (data != null) {
          console.log("Pre cricket came");
          //console.log(data[1].markets[0].market.runners[0].selectionId);
          setCricketData(data);
          //setCricketData(data.cricketData);
        } else {
          console.log("came but null");
        }
      })
    }
  }, [user, token, isSocketConnected])

  // useEffect(() => {
  //   if (user && isSocketConnected) {
  //     socket.emit('get pregame data');
  //     console.log("other sports data")
  //     //return;
  //     socket.off("get pregame data").on("get pregame data", (data) => {
  //       if (data != null) {
  //         console.log("Pre game came");
  //         //console.log(data);
  //         if(pregames?.length != data.length){
  //           setPreGames(data);
  //         }
  //         //setCricketData(data.cricketData);
  //       } else {
  //         console.log("came but null");
  //       }
  //     })
  //   }
  // }, [user, token, isSocketConnected])

  useEffect(() => {
    if(games && pregames && updateBet && precricketgames ){
      bets.forEach(bet => {
        //find the game related to the bet
        const [foundGame, gameType] = getGame(bet.gameId);
        if(gameType != "precricket"){
          if(foundGame){
            //check if the game has optionMarkets
            if(foundGame.optionMarkets[0]){
              //find which category the bet belongs
              var foundCategoryIndex = foundGame.optionMarkets.findIndex(option => option.id == bet.categoryId);
              //check if the category exists and visible
              if(foundCategoryIndex < 0 || foundGame.optionMarkets[foundCategoryIndex].status != 'Visible'){
                //if not found and not visible set odds suspended 
                updateBetOdds(bet._id, 'suspended');
                calculateTotalReturn();
              } else{
                // else if the category is found and visible check if the optionId matches with bet
                foundGame.optionMarkets[foundCategoryIndex].options.forEach(option => {
                  
                  if(option.id == bet.optionId){
                    //if so update the odds of the bet
                    updateBetOdds(bet._id, option.price.odds);
                    calculateTotalReturn();
                  }
                })
              }
            } else if(foundGame.Markets[0]){ //check if the game has Markets
              //find which category the bet belongs
              var foundCategoryIndex = foundGame.Markets.findIndex(option => option.id == bet.categoryId);
              //check if the category exists and visible
              if(foundCategoryIndex < 0 || foundGame.Markets[foundCategoryIndex].visibility != 'Visible'){
                //if not found and not visible set odds suspended 
                updateBetOdds(bet._id, 'suspended');
                calculateTotalReturn();
              } else{
                // else if the category is found and visible check if the optionId matches with bet
                foundGame.Markets[foundCategoryIndex].results.forEach(option => {
                  if(option.id == bet.optionId){
                    //if so update the odds of the bet
                    updateBetOdds(bet._id, option.odds);
                    calculateTotalReturn();
                  }
                })
              }
            } else{
              // if the game doesn't have optionMarket & Market set the odds to suspended
              updateBetOdds(bet._id, 'suspended');
              calculateTotalReturn();
            }
          }  else{
            // if the game doesn't have optionMarket & Market set the odds to suspended
            deleteBet(bet._id);
            calculateTotalReturn();
          }
        } else{
          if(foundGame){
            //check if the game has optionMarkets
            if(foundGame.markets[0]){
              //find which category the bet belongs
              var foundCategoryIndex = foundGame.markets.findIndex(option => option.market.marketId == bet.categoryId);
              //check if the category exists and visible
              if(foundCategoryIndex < 0 || foundGame.markets[foundCategoryIndex].marketStatus != 'OPEN'){
                //if not found and not visible set odds suspended 
                updateBetOdds(bet._id, 'suspended');
                calculateTotalReturn();
              } else{
                // else if the category is found and visible check if the optionId matches with bet
                foundGame.markets[foundCategoryIndex].runnerDetails.forEach(option => {
                  
                  if(option.selectionId == bet.optionId){
                    //if so update the odds of the bet
                    updateBetOdds(bet._id, option.runnerOdds.decimalDisplayOdds.decimalOdds);
                    calculateTotalReturn();
                  }
                })
              }
            } else{
              // if the game doesn't have optionMarket & Market set the odds to suspended
              updateBetOdds(bet._id, 'suspended');
              calculateTotalReturn();
            }
          }  else{
            // if the game doesn't have optionMarket & Market set the odds to suspended
            deleteBet(bet._id);
            calculateTotalReturn();
          }
        }
      })
    }
  }, [games, updateBet, pregames, precricketgames]);

  useEffect(() => {
    if (user && isSocketConnected) {
      async function postData(url = '') {
        // Default options are marked with *
        const response = await fetch(url, {
          method: 'GET', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          headers: {
            'Authorization': 'Bearer ' + token,
            // 'Content-Type': 'application/x-www-form-urlencoded',
          }
          // body data type must match "Content-Type" header
        });
        return response.json(); // parses JSON response into native JavaScript objects
      }

      postData(`${process.env.REACT_APP_URL_LINK}/api/notifications`)
        .then(data => {
          //setNotifications(data.filter(n => n.userFrom._id != user._id));
        }).catch(err => console.log(err));
    }
  }, [user, token, isSocketConnected])

  const getGame = (gameId) => {
    if (games != null) {
      var foundGame = games.find(game => game.Id == gameId);
      if(foundGame != null){
          return [foundGame, 'game'];
      } else{
          if(pregames != null){
              foundGame = pregames.find(game => game.Id == gameId);
              if(foundGame != null){
                return [foundGame, 'pregame'];
              } else{
                if(precricketgames != null){
                    foundGame = precricketgames.find(game => game.event.eventId == gameId);
                    //console.log()
                    if(foundGame != null){
                      return [foundGame, 'precricket'];
                    } else{
                        return [null, null];
                    }
                } else{
                    return [null, null];
                }
              }
          } else{
              return [null, null];
          }
      }
    } else {
      return [null, null];
    }
}



  return (
        <Suspense fallback={"Loading..."}>
          <Routes>
            <Route path="/" element={user ? <HomePage /> : <Navigate to="/login" replace />} />
            <Route path="/profile/:id" element={user ? <Profile /> : <Navigate to="/login" replace />} />
            <Route path="/post/:id" element={user ? <PostPage /> : <Navigate to="/login" replace />} />
            <Route path="/chat/:id" element={user ? <ChatPage /> : <Navigate to="/login" replace />} />
            <Route path="/login" element={user ? <Navigate to="/" replace /> : <Login />} />
            <Route path="/messages" element={user ? <Inbox /> : <Navigate to="/login" replace />} />
            <Route path="/register" element={user ? <Navigate to="/" replace /> : <Register />} />
            <Route path="/search" element={user ? <SearchPage /> : <Navigate to="/login" replace />} />
            <Route path="/notifications" element={user ? <NotificationPage /> : <Navigate to="/login" replace />} />
            <Route path="/new-message" element={user ? <NewMessagePage /> : <Navigate to="/login" replace />} />
            <Route path="/sports" element={user ? <SportsPage /> : <Navigate to="/login" replace />} />
            <Route path="/prematch" element={user ? <PreSportsPage /> : <Navigate to="/login" replace />} />
            <Route path="/game/:id" element={user ? <GamePage /> : <Navigate to="/login" replace />} />
            <Route path="/my-bets" element={user ? <MyBet /> : <Navigate to="/login" replace />} />
            <Route path="/quiz-overview" element={user ? <QuizOverview /> : <Navigate to="/login" replace />} />
            <Route path="/followers-following/:profileId/:tab" element={user ? <FollowersAndFollowingPage /> : <Navigate to="/login" replace />} />
          </Routes>
        </Suspense>
  );
}

const mapStateToProps = state => ({
  user: state.user.user,
  token: state.user.token,
  games: state.game.games,
  pregames: state.pregame.games,
  precricketgames:state.pregame.cricketData,
  bets: state.betslip.bets,
  updateBet: state.betslip.updateBet,
  betErrorMessage: state.betslip.errorMessage
})

const mapDispatchToProps = dispatch => ({
  setGames: games => dispatch(setNewGames(games)),
  setPreGames: games => dispatch(setPreGames(games)),
  setCricketData:games => dispatch(setCricketData(games)),
  setNotifications: notifications => dispatch(putNotifications(notifications)),
  updateBetOdds: (betId, odds) => dispatch(updateBetOdds(betId, odds)),
  deleteBet: (id) => dispatch(deleteBet(id)),
  setErrorMessage: (value) => dispatch(setErrorMessage(value)),
  calculateTotalReturn: () => dispatch(calculateTotalReturn())
})


export default connect(mapStateToProps, mapDispatchToProps)(App);
