import ActionTypes from './pregame.types';

const INITIAL_STATE = {
    games:null,
    cricketData:null
}

export const preGameReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ActionTypes.SET_PREGAMES:
          return {
                ...state,
                games: action.games
            };
        case ActionTypes.SET_PRE_CRICKET_DATA:
            return {
                    ...state,
                    cricketData: action.games
                };
        default:
            return state;
      }
}