import React, { useState, useContext } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

import { userLogin } from '../../redux/user/user.actions';

//import { AuthContext } from '../../context/auth-context';

import '../login/login.css'

function Register({ login }) {
	
	//const { token, login, logout, user } = useContext(AuthContext); 
	
	const [errorMessage, setErrorMessage] = useState(null);

	const [formData, setformData] = useState({
		firstName: '',
		lastName: '',
		username:'',
		email: '',
		password: '',
		confirmPassword: ''
	})

	const handleChange = (event) => {
		setformData(prev => {
			return {
				...prev,
				[event.target.name]: event.target.value
			}

		})
	}

	const handleSubmit = (event) => {
		event.preventDefault();
		// Example POST method implementation:
		async function postData(url = '', data = {}) {
			// Default options are marked with *
			const response = await fetch(url, {
			method: 'POST', // *GET, POST, PUT, DELETE, etc. // include, *same-origin, omit
			headers: {
				'Content-Type': 'application/json'
				// 'Content-Type': 'application/x-www-form-urlencoded',
			},
			mode: 'cors', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
			body: JSON.stringify(data) // body data type must match "Content-Type" header
			});
			return response.json(); // parses JSON response into native JavaScript objects
		}
		
		postData('http://localhost:5000/register', { ...formData })
			.then(data => {
				if(data.errorMessage){
					setErrorMessage(data.errorMessage);
				}else{
					login(data.user, data.token);
				}
		}).catch(err => console.log(err));
	}
  return (
      <div className='formWrapper'>
      	<div className='loginContainer'>
      		<h1>Register</h1>
      		<form id='registerForm' onSubmit={handleSubmit}>
      			{errorMessage && <p className='errorMessage'>{errorMessage}</p>}
      			<input type="text" name="firstName" placeholder="First name" required onChange={handleChange} />
      			<input type="text" name="lastName" placeholder="Last name" required onChange={handleChange} />
      			<input type="text" name="username" placeholder=" Username" required onChange={handleChange} />
      			<input type="email" name="email" placeholder="Email Address" required onChange={handleChange} />
      			<input type="password" name="password" placeholder="Password" required onChange={handleChange} />
      			<input type="password" name="confirmPassword" placeholder="Confirm Password" required onChange={handleChange} />
      			<input type="submit" value="Register" />
      			<Link to='/login'>Already have an account? Login here.</Link>
      		</form>
      	</div>
      </div>
  );
}

const mapDispatchToProps = dispatch => ({
	login: (user, token) => dispatch(userLogin(user, token))
})

export default connect(null, mapDispatchToProps)(Register);
