import * as React from "react";
import { connect } from "react-redux";

import { setPageType } from '../../redux/page/page.actions';

import MainLayout from '../../layouts/main-layout.component';
import Post from '../../components/post/post.component';
import User from "../../components/user/user.component";


function SearchPage({ token, setPageType }) {

  const textbox = React.useRef(null);

  const [value, setValue] = React.useState("");
  const [tab, setTab] = React.useState('post');
  const [posts, setPosts] = React.useState(null);
  const [users, setUsers] = React.useState(null);

  const handleTabChange = () =>{
    tab === 'post' ? setTab('user') : setTab('post');
  }

  const handleType = (event) => {
    setValue(event.target.value);
  }

  React.useLayoutEffect(() => {
    setPageType('default');
  },[]);

  React.useEffect(() => {
    setPosts(null);
    setUsers(null);
    if(value.trim() !== ""){
      var url = tab === 'post' ? `http://localhost:5000/api/posts?search=${value}` : `http://localhost:5000/api/users?search=${value}`;
      // Example POST method implementation:
      async function putData(url = '') {
          // Default options are marked with *
          const response = await fetch(url, {
          method: 'GET', // *GET, POST, PUT, DELETE, etc. // include, *same-origin, omit
          headers: {
            'Authorization': 'Bearer ' + token
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          mode: 'cors'
        });
        return response.json(); // parses JSON response into native JavaScript objects
      }
      
      putData(url)
        .then(data => {
            //console.log(data);
            tab === 'post' ? setPosts(data) : setUsers(data);
            //textbox.current.focus();
            //login(data.user._doc, data.token);	
      }).catch(err => console.log(err));
    }
  },[tab, value]);

  return (
      	<MainLayout>
            <div className="searchBarContainer">
                <i className="fas fa-search"></i>
                <input id="searchBox" onChange={handleType} type="text" name="searchBox" value={value} placeholder="Search for users or posts" />
            </div>
            <div className="tabsContainer">
                <button onClick={handleTabChange} className={`tab ${tab === 'post' ? 'active' : ''}`}><span>Posts</span></button>
                <button onClick={handleTabChange} className={`tab ${tab === 'user' ? 'active' : ''}`}><span>Users</span></button>
            </div>
            <div className="resultsContainer">
                {
                    tab === 'post' && posts && posts.map(post => {
                      return <Post key={post._id} postData={post} />
                    })
                }
                {
                    tab === 'user' && users && users.map(user => {
                      return <User key={user._id} userData={user} showFollowButton={false} />
                    })
                }
            </div>
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
  token: state.user.token
})

const mapDispatchToProps = dispatch => ({
  setPageType: (pageType) => dispatch(setPageType(pageType))
})


export default connect(mapStateToProps, mapDispatchToProps)(SearchPage);