import React from "react";

import Sport from "../sports/sport.component";
import PreCricket from "../precricket/precricket.component";

function SportsList({ results, gameType }){
    const [formattedData, setFormattedData] = React.useState(null);

    React.useEffect(() => {
        if(gameType != "precricket"){
            let copyGames = {};
            results.sort(function(a, b){
                if(a.Scoreboard?.scoreboardId && b.Scoreboard?.scoreboardId){
                    return (a.Scoreboard.scoreboardId - b.Scoreboard.scoreboardId)
                } else if(a.IsPrematch && b.IsPrematch){
                    return (a.Date - b.Date)
                }
            }).forEach(result => {
                copyGames[result.LeagueName] ? 
                    copyGames[result.LeagueName] = [...copyGames[result.LeagueName], result ] 
                    : 
                    copyGames[result.LeagueName] = [result];
            })
            //console.log(copyGames);
            setFormattedData(copyGames);
        } else{
            let copyGames = {};
            results.sort(function(a, b){
                    return (a.event.openDate - b.event.openDate)
            }).forEach(result => {
                copyGames[result.competitions.name] ? 
                    copyGames[result.competitions.name] = [...copyGames[result.competitions.name], result ] 
                    : 
                    copyGames[result.competitions.name] = [result];
            })
            //console.log(copyGames);
            setFormattedData(copyGames);
        }
    },[results, gameType])
    

    return formattedData && results && Object.keys(formattedData).map(key => {
        return <React.Fragment key={key}>
            <div className="sport__header-container">
                {/* <i className="fa fa-baseball sport__header__icon"></i> */}
                <div className="sport__league">
                    {key}
                </div>
                <div className="sport__headers">
                    <div className="sport__header">
                        1
                    </div>
                    <div className="sport__header">
                        X
                    </div>
                    <div className="sport__header">
                        2
                    </div>
                </div>
            </div>
            {
                gameType != "precricket" ? formattedData[key].map(data => {
                    return <Sport key={data.Id} 
                    data={data} gameType={gameType} /> 
                }) 
                : 
                formattedData[key].map(data => {
                    return <PreCricket key={data.event.eventId} 
                    data={data} /> 
                })
            }
        </React.Fragment>
    })

}

export default SportsList;