export const calculateTotalReturn = (betslip) => {
    if(betslip.betType == 'single'){
        var total = 0;
        betslip.bets.forEach(bet => {
            if( bet.odds != 'suspended' && bet.stake > 0){
                total += (bet.odds * bet.stake);
            }
        })

        //betslip.singleTotalReturn = total;

        return {
            ...betslip,
            ['singleTotalReturn']: total
        };

    } else if(betslip.betType == 'multi'){
        var totalOdds = 1;
        var totalReturn = 0;
        betslip.bets.forEach(bet => {
            if( bet.odds != 'suspended'){
                totalOdds *= parseFloat(bet.odds);
            }
        })

        if(betslip.totalStake > 0){
            totalReturn = parseFloat(totalOdds) * parseFloat(betslip.totalStake);
        } else{
            totalReturn = 0;
        }
        

        return {
          ...betslip,
          ['multiTotalReturn']: totalReturn  
        }
    } else if(betslip.betType == 'system'){
        var totalReturn = 0;

        if(betslip.bets[0].odds != 'suspended' && betslip.bets[1].odds != 'suspended' && betslip.bets[2].odds != 'suspended' && betslip.totalStake > 0){
            var one = betslip.bets[0].odds * betslip.bets[1].odds;
            var two = betslip.bets[1].odds * betslip.bets[2].odds;
            var three = betslip.bets[0].odds * betslip.bets[2].odds;
            var totalOdds = one + two + three;
            totalReturn = totalOdds * (betslip.totalStake / 3);
        }

        return {
            ...betslip,
            ['systemTotalReturn']: totalReturn  
        }
    }
}