import React from "react";
import { Link } from 'react-router-dom';


function QuizView({ data, gameType }) {
    //console.log(data, gameType);
    //return <></>
    return (
        data && <div className='sport-container'>
            <Link to={`/game`}>
                <div className='sportContentContainer quiz-view'>
                    <div className="sportContentContainer__teams-container">
                        <div className="sportContentContainer__teams-items">
                                <i className="fa fa-shield" />
                                <p>{ 
                                    gameType != "Cricket" 
                                    ?
                                    data.HomeTeam ? data.HomeTeam : 'Unavailable'
                                    :
                                    data.event?.name.split("v")[0]
                                }</p>
                        </div>
                        <div className="sportContentContainer__teams-items">
                                <i className="fa fa-shield" />
                                <p>
                                    { 
                                        gameType != "Cricket" 
                                        ?
                                        data.AwayTeam ? data.AwayTeam : 'Unavailable'
                                        :
                                        data.event?.name.split("v")[1]
                                    }
                                </p>
                        </div>
                    </div>
                    <div className="sportContentContainer__btn-container">
                        <p className="sportContentContainer__btn-time">1hr left</p>
                        <button className="sportContentContainer__btn-item">Play</button>
                    </div>
                </div>
            </Link>
        </div>
    )
}


export default QuizView;
