import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { connect, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { createStructuredSelector } from 'reselect';

import { setUpdateBet, calculateTotalReturn, setBetType, setTotalStake, updateBetStake, setErrorMessage, setUseBonus, setErrorValue, clearBets } from '../../redux/betslip/betslip.actions';
import { userLogin } from '../../redux/user/user.actions';
import { selectBets, selectBetType, selectUpdateBet, selectTotalStake, selectSingleTotalReturn, selectMultiTotalReturn, selectSystemTotalReturn, selectErrorMessage, selectUseBonus, selectErrorValue } from '../../redux/betslip/betslip.selectors';

import Bet from '../bet/bet.component';

import './BetSlip.css';

//bets, updateBet, betType, totalStake, singleTotalReturn, multiTotalReturn, systemTotalReturn,

const BetSlip = (props) =>  { 
    const { t } = useTranslation(["common"]);
    const navigate = useNavigate();

    const {
        bets, 
        updateBet, 
        betType, 
        totalStake,
        errorMessage,
        errorValue, 
        singleTotalReturn, 
        multiTotalReturn, 
        systemTotalReturn, 
        setErrorMessage,
        setErrorValue,
        setUpdateBet, 
        setBetType, 
        setBetStake, 
        setTotalStake, 
        calculateTotalReturn,
        useBonus,
        clearBets,
        setUseBonus,
        login } = props;

       const token = useSelector((state) => state.user.token);

    const [collapsed, setCollapsed] = useState(bets.length > 1 ? true : false);
    const [isOpen, setIsOpen] = useState(false);
    const [invalid, setInvalid] = useState(false);
    const [formattedData, setFormattedData] = useState({});
    const [isLoading, setIsLoading] = useState(false);
    const [betsLength] = useState(bets.length);
    const [isSuccess, setIsSuccess] = useState(false);
    

    const handleStakeTypeChange = (e) => {
        if(e.target.dataset.type == 'multi' && bets.length > 8){
            return;
        } else if(e.target.dataset.type == 'system' && bets.length != 3){ 
            return;
        }
        setErrorMessage(null);
        setErrorValue(null);
        setBetType(e.target.dataset.type) 
        calculateTotalReturn();
    }

    const handleModal = () => {
        clearBets();
        setIsSuccess(false);
    }

    const handleOpen = () => {
        //setCollapsed(!collapsed);
        setIsOpen(!isOpen);
    }

    const handleUpdateBet = (e) => {
        if(e.target.checked){
            setUpdateBet(true);
        } else{
            setUpdateBet(false);
        }
    }

    const handleUpdateBonus = (e) => {
        if(e.target.checked){
            setUseBonus(true);
        } else{
            setUseBonus(false);
        }
    }

    const handleStakeChange = (e) => {
            if(betType == 'single'){
                setBetStake(bets[0]._id, e.target.value)
                calculateTotalReturn();
            } else {
                setTotalStake(e.target.value);
                calculateTotalReturn();
            }

            
            //console.logbets);
    }

    const handleSubmit = () => {
            // Example POST method implementation:
        async function postData(url = '', data = {}) {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, *cors, same-origin
            headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
                // 'Content-Type': 'application/x-www-form-urlencoded',
            }, // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
            body: JSON.stringify(data) // body data type must match "Content-Type" header
            });
            return response.json(); // parses JSON response into native JavaScript objects
        }

        if(!isLoading){
            setIsLoading(true);
            postData('http://localhost:5000/api/betting/submit-bets', { 
                bets,
                betType,
                totalStake,
                singleTotalReturn,
                multiTotalReturn,
                systemTotalReturn,
                useBonus
            }).then(data => {
                setIsLoading(false);
                console.log(data);
                setErrorMessage(null)
                setErrorValue(null)
                setIsSuccess(false);
                if(data.message){
                    setErrorMessage(data.message);
                    if(data.value >= 0){
                        setErrorValue(data.value);
                    }
                } else{
                    //PROCEED
                    login(data.user, data.token);
                    setIsSuccess(true);
                }
            }).catch(err => console.log(err))
        }
    }

    React.useEffect(() => {
        if(bets.length > 1){
            if(isOpen == false){
                setCollapsed(true);
            } else{
                setCollapsed(false);
            }
            
        } else {
            if(isOpen == false){
                setCollapsed(false);
            } else{
                setCollapsed(true);
            }
        }
    },[bets, isOpen]);

    React.useEffect(() => {
        setFormattedData({});
        if(betType == 'multi'){
            for(var i= 0; i < bets.length; i++){
                if(formattedData[bets[i].categoryId.toString()+bets[i].gameId.toString()]){
                    setInvalid(true);
                    setErrorMessage("betslip__multiCategoryErr");
                    setErrorValue(null)
                    return;
                } else{
                    setInvalid(false);
                    setErrorMessage(null);
                    setErrorValue(null);
                    formattedData[bets[i].categoryId.toString()+bets[i].gameId.toString()] = 1;
                }
            }
        } else if(betType == "system") {
            for(var i= 0; i < bets.length; i++){
                if(formattedData[bets[i].gameId.toString()]){
                    setInvalid(true);
                    setErrorMessage("betslip__systemCategoryErr");
                    setErrorValue(null);
                    return;
                } else{
                    setInvalid(false);
                    setErrorMessage(null);
                    setErrorValue(null);
                    formattedData[bets[i].gameId.toString()] = 1;
                }
            }
        }
    },[betType, betsLength]);


    React.useEffect(() => {
        if(errorMessage == "betslip__systemSelectionErr" || errorMessage == "betslip__singleSelectionErr" || errorMessage == "betslip__parlaySelectionErr" || errorMessage == "betslip__oddsMatchErr" || errorMessage == "betslip__disabledEvent" || errorMessage == "betslip__disabledCategory" || errorMessage == "betslip__globalMaxBet" || errorMessage == "betslip__globalMinBet" || errorMessage == "betslip__globalMaxWin" || errorMessage == "betslip__globalMinWin" || errorMessage == "betslip__userMaxBet" || errorMessage == "betslip__userMinBet" || errorMessage == "betslip__userMaxWin" || errorMessage == "betslip__couldNotSubmit" || errorMessage == "betslip__exceededUserBonus" || errorMessage == null){
            setInvalid(false)
        } else{
            setInvalid(true);
        }
    },[errorMessage]);



    React.useEffect(() => {
        if(bets.length < 3){
            // setErrorMessage(null);
            // setErrorValue(null);
            setBetType('single')
            setInvalid(false)
            calculateTotalReturn();
        }
        calculateTotalReturn();
    },[bets]);
   
    var totalOdds;

    if(betType == 'single'){
        totalOdds = 0;
        bets.forEach(bet => {
            if( bet.odds != 'suspended'){
                totalOdds += bet.odds;
            }
            //console.log('Hii');
        })
    } else if(betType == 'multi'){
        totalOdds = 1;
        bets.forEach(bet => {
            if( bet.odds != 'suspended'){
                totalOdds *= parseFloat(bet.odds);
            }
        })
    } else {
        totalOdds = 0;
    }

    //console.log(updatedGames)


    return <div className='betslip__container'>
        {isSuccess && <div className='betslip__success__container'>
            <div className='betslip__success__text'>
                <h1 className='betslip__success__title'>your bet was Successfuly accepted</h1>
                <button className='betslip__success__btn' onClick={handleModal}>
                    Ok
                </button>
            </div>
        </div>}

        <div className='betslip__badge' onClick={handleOpen}>
            <span>Bet Slip ({bets.length})</span>
            
            {collapsed ? <span><i className="fa fa-chevron-up" /></span> : <span><i className="fa fa-chevron-down" /></span>}
        </div>

            {(collapsed == false && bets.length > 1) && <div className='betslip__stakeType'>
                <div className={`betslip__stakeType__item ${betType == 'single' && "active"}`} onClick={handleStakeTypeChange} data-type='single'>
                    Single
                </div>
                {bets.length > 2 && bets.length < 9 && <div className={`betslip__stakeType__item ${betType == 'multi' && "active"}`} onClick={handleStakeTypeChange} data-type='multi'>
                    Multi/Parlay
                </div>}
                {bets.length == 3 && <div className={`betslip__stakeType__item ${betType == 'system' && "active"}`} onClick={handleStakeTypeChange} data-type='system'>
                    System 2/3
                </div>}
            </div> }
            
            {collapsed == false && errorMessage && <p className='betslip__errorMessage'>{t(errorMessage)} {errorValue >= 0 && errorValue}</p>}
            <div className='betslip__body__container'>
                    {
                        collapsed == false && bets.map(bet => (
                            <Bet 
                                key={bet._id} 
                                id={bet._id} 
                                title={bet.optionName} 
                                odds={bet.odds} category={bet.category} 
                                teams={bet.teams}
                                stake={bet.stake} 
                                stakeType={betType} />
                        ))
                    }
            </div>
            
            
        {!collapsed && <div className="betslip__footer">
            {isLoading && <div id="stripes" className="stripes"></div>}
            {((betType == 'multi' || betType == 'system') || bets.length < 2) && <div className="betslip__stake">
                <input type='number' className='betslip__stake__input' placeholder='Stake' value={betType == 'single' ? (bets[0].stake > 0 && bets[0].stake ) : (totalStake > 0 && totalStake )} onChange={handleStakeChange} />
            </div>}
             <button className="betslip__btn__saving" disabled={invalid} onClick={handleSubmit}>
                <span className="betslip__btn__saving__text">Place Bet</span>
                <span className='totals'>
                    <span className='totals__text'>
                        Total Odds: {parseFloat(totalOdds).toFixed(3)}
                    </span>
                    {betType == 'single' && <span>Total Return: {singleTotalReturn > 0 ? parseFloat(singleTotalReturn).toFixed(2) : singleTotalReturn}</span>}
                    {betType == 'multi' && <span>Total Return: {multiTotalReturn > 0 ? parseFloat(multiTotalReturn).toFixed(2) : multiTotalReturn}</span>}
                    {betType == 'system' && <span>Total Return: {systemTotalReturn > 0 ? parseFloat(systemTotalReturn).toFixed(2) : systemTotalReturn}</span>}
                    
                </span> 
                
            </button>
        </div>}
        {!collapsed && <div className='betslip__badge'>
         <div className="form-check">
            {updateBet 
            ? 
            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" onClick={handleUpdateBet} defaultChecked />
            :
            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" onClick={handleUpdateBet} />
            }
            <label className="form-check-label" htmlFor="flexCheckDefault">
                Accept Change
            </label>
            </div> 
        </div>}
        {!collapsed && <div className='betslip__badge'>
         <div className="form-check">
            {useBonus 
            ? 
            <input className="form-check-input" type="checkbox" value="" id="flexCheckuseBonus" onClick={handleUpdateBonus} defaultChecked />
            :
            <input className="form-check-input" type="checkbox" value="" id="flexCheckuseBonus" onClick={handleUpdateBonus} />
            }
            <label className="form-check-label" htmlFor="flexCheckuseBonus">
                Use Bonus
            </label>
            </div> 
        </div>}
    </div> 
}

const mapStateToProps = createStructuredSelector({
    bets: selectBets,
    betType: selectBetType,
    updateBet: selectUpdateBet,
    totalStake: selectTotalStake,
    errorMessage: selectErrorMessage,
    errorValue: selectErrorValue,
    singleTotalReturn: selectSingleTotalReturn,
    multiTotalReturn: selectMultiTotalReturn,
    systemTotalReturn: selectSystemTotalReturn,
    useBonus:selectUseBonus
});

const mapDispatchToProps = dispatch => ({
    setUpdateBet: value => dispatch(setUpdateBet(value)),
    setBetType: value => dispatch(setBetType(value)),
    setTotalStake: value => dispatch(setTotalStake(value)),
    setBetStake: (id, value) => dispatch(updateBetStake(id, value)),
    setErrorMessage:(value) => dispatch(setErrorMessage(value)),
    setErrorValue:(value) => dispatch(setErrorValue(value)),
    calculateTotalReturn: () => dispatch(calculateTotalReturn()),
    setUseBonus: (value) => dispatch(setUseBonus(value)),
    clearBets: () => dispatch(clearBets()),
    login: (user, token) => dispatch(userLogin(user, token))
})

export default connect(mapStateToProps, mapDispatchToProps)(BetSlip); 

