import ActionTypes from './game.types';

const INITIAL_STATE = {
    games:null,
    updatedGame: true
}

export const gameReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ActionTypes.SET_GAMES:
          return {
                ...state,
                games: action.games
            };
          break;
          case ActionTypes.UPDATED_GAMES:
          return {
                ...state,
                updatedGame: !state.updatedGame
            };
          break;
        default:
            return state;
      }
}