import * as React from "react";
import { connect } from "react-redux";

import { setOddType } from "../../redux/oddType/oddType.actions";
import { setPageType } from '../../redux/page/page.actions';

import MainLayout from '../../layouts/main-layout.component';
import SportsList from "../../components/sportsList/sportsList.component";
import BetSlip from "../../components/betslip/BetSlip";

import { socket, useSocket, isSocketConnected } from "../../socket/socket";



function PreSportsPage({ user, token, games, cricketgames, oddType, setOddType, bets, setPageType }) {
    const [sortedGames] = React.useState(['Cricket', 
    'Football', 'Tennis', 'Baseball', 'Basketball', 'Volleyball','Table Tennis',
    'Badminton','American Football', 'Futsal', 'Handball', 'Ice Hockey', 'Snooker',
    'Darts', 'Rugby Union', 'Rugby League', 'Boxing', 'Aussie Rules', 'Formula 1', 
    'Golf', 'Motorbikes', 'Cycling', 'Alpine Skiing', 'Biathlon', 'E-Sports'])
    const [ sportsData, setSportsData ] = React.useState([]);
    const [formattedData, setFormattedData] = React.useState(null);
    const [ notFound, setNotFound ] = React.useState(true);
    
    useSocket();

    React.useLayoutEffect(() => {
        setPageType('sports');
    },[]);

    React.useEffect(() => {
        if(user){
            if(games != null || games != undefined){
                //console.log(data.results);
                let copyGames = {};
                games.forEach(result => {
                    copyGames[result.SportName] ? 
                        copyGames[result.SportName] = [...copyGames[result.SportName], result ] 
                        : 
                        copyGames[result.SportName] = [result];
                })
                //console.log(copyGames);
                setNotFound(false)
                setFormattedData(copyGames);
                setSportsData(games);
            } else{
                setSportsData([]);
                setNotFound(true);
            }
        }
    },[user, token, games])

    React.useEffect(() => {
        if(user){
            if(cricketgames != null || cricketgames != undefined){
                //console.log(data.results);
                // let copyGames = {};
                // games.forEach(result => {
                //     copyGames[result.SportName] ? 
                //         copyGames[result.SportName] = [...copyGames[result.SportName], result ] 
                //         : 
                //         copyGames[result.SportName] = [result];
                // })
                // //console.log(copyGames);
                setNotFound(false)
                // setFormattedData(copyGames);
                // setSportsData(games);
            } else{
                //setSportsData([]);
                setNotFound(true);
            }
        }
    },[user, token, cricketgames])


  return (
      	<MainLayout>
              <div className="sport__advertisement">
                
              </div>
              <div className="sports-carousel-container">
                    <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
                            <div className="carousel-inner">
                                <div className="carousel-item active" style={{background:"url('https://images.unsplash.com/photo-1471295253337-3ceaaedca402?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8OHx8c3BvcnRzfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60')"}}>
                                </div>
                                <div className="carousel-item" style={{background:"url('https://images.unsplash.com/photo-1622279457486-62dcc4a431d6?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8dGVubmlzfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60')"}}>
                                    
                                </div>
                                <div className="carousel-item" style={{background:"url('https://images.unsplash.com/photo-1530549387789-4c1017266635?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTB8fHNwb3J0c3xlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60')"}}>
                                    
                                </div>
                            </div>
                            <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span className="sr-only">Previous</span>
                            </a>
                            <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                <span className="sr-only">Next</span>
                            </a>
                    </div>
              </div>
              <div className="sport__advertisement">
        
              </div>
              <label htmlFor="oddSwitch">Odds Switch {oddType}</label>
              <select className="form-select form-select-sm" id={`oddSwitch`} onChange={(e) => setOddType(e.target.value)}>
                <option value="decimal">Decimal</option>
                <option value="fractional">Fractional</option>
                <option value="moneyline">Moneyline</option>
                <option value="hongKong">HongKong</option>
                <option value="indonesian">Indonesian</option>
                <option value="malay">Malay</option>
                <option value="impliedProbability">Implied Probability</option>
            </select>
            <div className="resultsContainer">
                {
                    notFound && <span className="noResults">Fetching Results Please Wait...</span>
                }

                {
                    user && !notFound && cricketgames && <>
                    
                        <React.Fragment>
                            <h1 className="game-page__title">Cricket</h1>
                            {
                                <SportsList 
                                    results={cricketgames}
                                    gameType={'precricket'}
                                />    
                            }
                        </React.Fragment>
                    
                    </>     
                }

                {
                    user && !notFound && games && formattedData && <>
                    {
                        sortedGames.map(key => {
                            if(key in formattedData){
                                return (<React.Fragment key={key}>
                                    <h1 className="game-page__title">{key}</h1>
                                    {
                                        <SportsList 
                                            results={formattedData[key]}
                                            gameType={'pregame'}
                                        />    
                                    }
                                </React.Fragment>)
                            } else return null
                        })
                    }
                    </>     
                }
                {bets.length > 0 && <BetSlip />}
            </div>
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token,
    games: state.pregame.games,
    cricketgames: state.pregame.cricketData,
    oddType: state.oddType.oddType,
    bets: state.betslip.bets
})

const mapDispatchToProps = dispatch => ({
    setOddType: oddType => dispatch(setOddType(oddType)),
    setPageType: (pageType) => dispatch(setPageType(pageType))
})
 

export default connect(mapStateToProps, mapDispatchToProps)(PreSportsPage);