import * as React from "react";
import { useParams } from "react-router-dom";
import { connect } from "react-redux";

import { setPageType } from '../../redux/page/page.actions';

import MainLayout from '../../layouts/main-layout.component';
import InvidualGamePage from "../individualGame/individualGamePage.component";

import { useSocket, isSocketConnected } from "../../socket/socket";


function GamePage({ user, token, games, pregames, precricketgames, setPageType }) {
    const [isFetching, setIsFetching] = React.useState(true);
    const [game, setGame] = React.useState(null);
    const [gameType, setGameType] = React.useState(null);
    useSocket();

    const gameId = useParams().id; 

    const getGame = (gameId) => {
        if (games != null) {
          var foundGame = games.find(game => game.Id == gameId);
          if(foundGame != null){
              return [foundGame, 'game'];
          } else{
              if(pregames != null){
                  foundGame = pregames.find(game => game.Id == gameId);
                  if(foundGame != null){
                    return [foundGame, 'pregame'];
                  } else{
                    if(precricketgames != null){
                        foundGame = precricketgames.find(game => game.event.eventId == gameId);
                        //console.log()
                        if(foundGame != null){
                          return [foundGame, 'precricket'];
                        } else{
                            return [null, null];
                        }
                    } else{
                        return [null, null];
                    }
                  }
              } else{
                  return [null, null];
              }
          }
        } else {
          return [null, null];
        }
    }

    React.useLayoutEffect(() => {
        setPageType('sports');
      },[]);

    React.useEffect(() => {
        if(user && isSocketConnected){
            if(games != null && pregames != null && precricketgames != null){
                setIsFetching(false)
                var [ game, gameType ] = getGame(gameId);
                setGame(game);
                setGameType(gameType);
            } else {
                setIsFetching(true);
            }

            // if(precricketgames != null){
            //     setIsFetching(false)
            //     var [ game, gameType ] = getGame(gameId);
            //     setGame(game);
            //     setGameType(gameType);
            // } else {
            //     setIsFetching(true);
            // }  
        }
    },[user, token, gameId, games, pregames, precricketgames])

  return (
      	<MainLayout>
            <div className="resultsContainer">
                {
                    isFetching && <span className="noResults">Fetching Results Please Wait...</span>
                }
                
                {
                    user && !game && !isFetching && <span className="noResults">No Game Was Found or Maybe The Game Has Ended</span>
                }
                
                {
                    user && game && <InvidualGamePage game={game} gameType={gameType} />
                }
            </div>
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token,
    games: state.game.games,
    pregames:state.pregame.games,
    precricketgames:state.pregame.cricketData
})

const mapDispatchToProps = dispatch => ({
    setPageType: (pageType) => dispatch(setPageType(pageType))
})
  
  
export default connect(mapStateToProps, mapDispatchToProps)(GamePage);
  