
import { connect } from "react-redux";
import { updateNotification } from "../../redux/notification/notification.actions";

import { Link } from 'react-router-dom';


function Notification({ notification, user, token, updateNotification }) {
  //const { updateNotification } = useContext(NotificationContext);

    function getNotificationText(notification){
        var userFrom = notification.userFrom;

        if(!userFrom.firstName || !userFrom.lastName){
            return alert("user from data not populed");
        }

        var userFromName = `${userFrom.firstName} ${userFrom.lastName}`;

        var text;

        if(notification.notificationType == "retweet"){
            text = `${userFromName} retweeted one of your posts`;
        } else if(notification.notificationType == "postLike"){
            text = `${userFromName} liked one of your posts`;
        } else if(notification.notificationType == "reply"){
            text = `${userFromName} replied to one of your posts`;
        }  else if(notification.notificationType == "follow"){
            text = `${userFromName} followed you`;
        }

        return <span className='elipsis'>{text}</span>
    }

    function getNotificationUrl(notification){

        var url='#';

        if(notification.notificationType == "retweet" || notification.notificationType == "postLike" || notification.notificationType == "reply"){
            url = `/post/${notification.entityId}`;
        }  else if(notification.notificationType == "follow"){
            url = `/profile/${notification.entityId}`;
        }

        return url;
    }

    var userFrom = notification.userFrom;
    var text = getNotificationText(notification);
    var href = getNotificationUrl(notification);
    var className = notification.opened ? "" : "active";

    const handleMarking = (event) =>{
        event.preventDefault();
        //Example POST method implementation:
        async function putData(url = '') {
            // Default options are marked with *
            const response = await fetch(url, {
            method: 'PUT', // *GET, POST, PUT, DELETE, etc. // include, *same-origin, omit
            headers: {
                'Authorization': 'Bearer ' + token,
                'Content-Type': 'application/json'
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors'
            });
            return response.json(); // parses JSON response into native JavaScript objects
        }
        
        putData(`http://localhost:5000/api/notifications/${notification._id}/markAsOpened`)
            .then(data => {
                    console.log(data);
                    updateNotification(data);
                    window.location.href = href;
                    //login(data.user._doc, data.token);	
        }).catch(err => console.log(err));
    }
    
  
  return user && <Link to={href} onClick={handleMarking} className={`resultListItem notification ${className}`} data-id={`${notification._id}`}>
        <div className='resultsImageContainer'>
            <img src={`http://localhost:5000${userFrom.profilePic}`} />
        </div>
        <div className='resultsDetailsContainer ellipsis'>
            <span className='ellipsis'>{text}</span> 
        </div>
    </Link>
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token
})

const mapDispatchToProps = dispatch => ({
    updateNotification: notification => dispatch(updateNotification(notification))
})
  
  
export default connect(mapStateToProps, mapDispatchToProps)(Notification);
