import ActionTypes from './betslip.types';

export const setUpdateBet = (value) => ({
    type:ActionTypes.SET_UPDATE_BET,
    value: value
});

export const setBetType = (value) => ({
    type:ActionTypes.SET_BET_TYPE,
    value: value
});

export const setTotalStake = (value) => ({
    type:ActionTypes.SET_TOTAL_STAKE,
    value: value
});

export const setErrorMessage = (msg) => ({
    type:ActionTypes.SET_ERROR_MESSAGE,
    errorMessage:msg
});

export const setErrorValue = (value) => ({
    type:ActionTypes.SET_ERROR_VALUE,
    value:value
});

export const putBet = (bet) => ({
    type:ActionTypes.PUT_BET,
    value: bet
});

export const replaceBet = (index, bet) => ({
    type:ActionTypes.REPLACE_BET,
    oldBetIndex: index,
    newBet: bet
});

export const updateBetOdds = (betId, odds) => ({
    type:ActionTypes.UPDATE_BET_ODD,
    betId:betId,
    value: odds
});

export const updateBetStake = (betId, stake) => ({
    type:ActionTypes.UPDATE_BET_STAKE,
    betId:betId,
    value: stake
});

export const deleteBet = (betId) => ({
    type:ActionTypes.DELETE_BET,
    betId:betId
});

export const calculateTotalReturn = () => ({
    type:ActionTypes.CALCULATE_TOTAL_RETURN
});

export const clearBets = () => ({
    type:ActionTypes.CLEAR_BETS
});

export const setUseBonus = (value) => ({
    type:ActionTypes.SET_USE_BONUS,
    value: value
});