import React from "react";
import { Link } from 'react-router-dom';
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import { oddTypeChange } from '../../utils/oddsTypeChange';
import { addBet } from "../../utils/addbet";
import { calculateTotalReturn } from "../../redux/betslip/betslip.actions";


function Sport({ data, gameType, oddType, bets, betType }) {
    let odds;
    if (data.optionMarkets[0] && data.optionMarkets[0].options && data.optionMarkets[0].status == 'Visible') {
        if (data.optionMarkets[0].options.length == 3) {
            odds = <>{data.optionMarkets[0].options.map(option => {
                return <p key={option.name.value} className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == option.id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == option.id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: option.name.value,
                    category: data.optionMarkets[0].name.value,
                    categoryId:data.optionMarkets[0].id,
                    gameId: data.Id,
                    optionId:option.id,
                    odds:option.price.odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                }, betType, bets)} >{ option.price.odds <= 1 ? option.price.odds : oddTypeChange(option.price.odds, oddType)}</p>
            })}
            </>
        } else {
            odds = <>
                <p className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == data.optionMarkets[0].options[0].id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == data.optionMarkets[0].options[0].id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: data.optionMarkets[0].options[0].name.value,
                    category: data.optionMarkets[0].name.value,
                    categoryId:data.optionMarkets[0].id,
                    gameId: data.Id,
                    optionId:data.optionMarkets[0].options[0].id,   
                    odds:data.optionMarkets[0].options[0].price.odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                },betType,bets)}>{data.optionMarkets[0].options[0].price.odds <= 1 ? data.optionMarkets[0].options[0].price.odds : oddTypeChange(data.optionMarkets[0].options[0].price.odds, oddType)}</p>
                <p className="sport__odds__single">--</p>
                <p className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].name.value,
                    category: data.optionMarkets[0].name.value,
                    categoryId:data.optionMarkets[0].id,
                    gameId: data.Id,
                    optionId:data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].id,   
                    odds:data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].price.odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                }, betType, bets)}>{data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].price.odds <= 1 ? data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].price.odds : oddTypeChange(data.optionMarkets[0].options[data.optionMarkets[0].options.length - 1].price.odds, oddType)}</p>
            </>
        }
    } else if (data.Markets[0] && data.Markets[0].results && data.Markets[0].visibility == 'Visible') {
        if (data.Markets[0].results.length == 3) {
            odds = <>{data.Markets[0].results.map(option => {
                return <p key={option.name.value} className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == option.id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == option.id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: option.name.value,
                    category: data.Markets[0].name.value,
                    categoryId:data.Markets[0].id,
                    gameId: data.Id,
                    optionId:option.id,
                    odds:option.odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                },betType, bets)}>{option.odds <= 1 ? option.odds : oddTypeChange(option.odds, oddType)}</p>
            })}
            </>
        } else {
            odds = <>
                <p className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == data.Markets[0].results[0].id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == data.Markets[0].results[0].id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: data.Markets[0].results[0].name.value,
                    category: data.Markets[0].name.value,
                    categoryId:data.Markets[0].id,
                    gameId: data.Id,
                    optionId:data.Markets[0].results[0].id,
                    odds:data.Markets[0].results[0].odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                },betType,bets)}>{data.Markets[0].results[0].odds <= 1 ? data.Markets[0].results[0].odds : oddTypeChange(data.Markets[0].results[0].odds, oddType)}</p>
                <p className="sport__odds__single">--</p>
                <p className={`sport__odds__single ${bets.findIndex(bet => bet.optionId == data.Markets[0].results[data.Markets[0].results.length - 1].id) >= 0 && "active" }`} onClick={() => bets.findIndex(bet => (bet.optionId == data.Markets[0].results[data.Markets[0].results.length - 1].id && bet.gameId == data.Id)) < 0 && addBet({
                    _id: uuidv4(),
                    optionName: data.Markets[0].results[data.Markets[0].results.length - 1].name.value,
                    category: data.Markets[0].name.value,
                    categoryId:data.Markets[0].id,
                    gameId: data.Id,
                    optionId:data.Markets[0].results[data.Markets[0].results.length - 1].id,
                    odds:data.Markets[0].results[data.Markets[0].results.length - 1].odds,
                    stake:0,
                    teams:`${data.HomeTeam ? data.HomeTeam : 'Unavalilable'} V/S ${data.AwayTeam ? data.AwayTeam : 'Unavalilable'}`,
                    gameType:gameType
                },betType,bets)}>{data.Markets[0].results[data.Markets[0].results.length - 1].odds <= 1 ? data.Markets[0].results[data.Markets[0].results.length - 1].odds : oddTypeChange(data.Markets[0].results[data.Markets[0].results.length - 1].odds, oddType)}</p>
            </>
        }
    } else {
        odds = <p className="suspended">Suspended</p>
    }

    let score;

    if (data.SportId == 22) {
        score = gameType == 'game' ? (data.Scoreboard ? `${data.Scoreboard.runs}/${data.Scoreboard.wickets}` : '--') : `${new Date(data.Date).toLocaleDateString()}` ;
    } else {
        score = gameType == 'game' ? (data.Scoreboard?.score ? data.Scoreboard.score : '0:0') : `${new Date(data.Date).toLocaleDateString()}`;
    }

    const handleClick = (e) => {
        e.preventDefault();
    }

    return (
        <div className='sport-container'>
            <Link to={`/game/${data.Id}`}>
                <div className='sportContentContainer'>
                    <div className="sport__score">
                        {gameType == 'game' ? <i className="fa fa-signal sport__score__icon"></i> : `${new Date(data.Date).toLocaleString("en-us", { hour: '2-digit', minute: '2-digit'}).replace('AM', '').replace('PM', '')}`}
                        <span className="sport__score__content">
                            {score}
                        </span>
                    </div>
                    <div className="sport__teams">
                        <span className="sport__teams__single">{
                            data.HomeTeam ? data.HomeTeam : 'Unavailable'
                        }</span>
                        <span className="sport__teams__single">{
                            data.AwayTeam ? data.AwayTeam : 'Unavailable'
                        }</span>
                    </div>
                    <div className="sport__odds" data-id="sport_odd" onClick={handleClick}>
                        {odds}
                    </div>
                </div>
            </Link>
        </div>
    )
}

const mapStateToProps = state => ({
    oddType: state.oddType.oddType,
    bets: state.betslip.bets,
    betType: state.betslip.betType
})

const mapDispatchToProps = dispatch => ({
    calculateTotalReturn: () => dispatch(calculateTotalReturn())
})

export default connect(mapStateToProps, mapDispatchToProps)(Sport);
