import { useState, useEffect } from "react";
import { connect } from "react-redux";

import { putResults } from "../../redux/result/result.actions";

import Modal from "../modal/Modal";


import './gamescore.css';

function GameScore({ game, gameType, user, token, results, putResults }) {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [events, setEvents] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        if (user && results) {
            if (Array.isArray(game.Scoreboard?.messages) && game.Scoreboard?.messages.length == 0) {
                //console.log(results);
                var foundResult = results.findIndex(res => (res.bwin_id == parseInt(game.Id.toString().replace(":", "0")) || (res.id == parseInt(game.Id.toString().replace(":", "0")))))
                //console.log(foundResult);
                if (foundResult == -1) {
                    async function postData(url = '') {
                        // Default options are marked with *
                        const response = await fetch(url, {
                            method: 'GET', // *GET, POST, PUT, DELETE, etc.
                            mode: 'cors', // no-cors, *cors, same-origin
                            headers: {
                                'Authorization': 'Bearer ' + token,
                                // 'Content-Type': 'application/x-www-form-urlencoded',
                            }
                            // body data type must match "Content-Type" header
                        });
                        //console.log(url);
                        return response.json(); // parses JSON response into native JavaScript objects
                    }
                    setIsLoading(true);
                    postData(`${process.env.REACT_APP_URL_LINK}/result/${game.Id}`)
                        .then(data => {
                            //console.log(data.events);
                            setIsLoading(false);
                            if (data.events && events.length < 1) {
                                //console.log(data);
                                setEvents(data.events.reverse());
                            }
                            putResults(data);
                            //console.log(results)


                            // setEvents([])
                            // setEvents(data.events); // JSON data parsed by `data.json()` call
                        }).catch(err => console.log(err));
                } else if (events.length < 1 && results[foundResult].events) {
                    // if (foundResult != -1 && ) {
                        setEvents(results[foundResult].events);
                    //}
                }
            }
        }
    }, [user, results]);


    var content;
    if (game.SportId == 22) {
        content = <>
            <div className="gamescore__flex-container">
                <div className="gamescore__flex-item teamname">
                    {game.HomeTeam}
                </div>
                <div className="gamescore__flex-item vs">
                    V/S
                </div>
                <div className="gamescore__flex-item teamname">
                    {game.AwayTeam}
                </div>
            </div>
            <div className="gamescore__flex-container">
                <div className="gamescore__flex-item score">
                    {gameType == 'game' ? (game.Scoreboard ? 
                        `${game.Scoreboard.turn == "Player01" ? game.HomeTeam : game.AwayTeam} ${game.Scoreboard.runs}/${game.Scoreboard.wickets} off ${game.Scoreboard.overs} overs`
                        : 
                        "-- : --") : `${new Date(game.Date).toLocaleTimeString()} ${new Date(game.Date).toLocaleDateString()}`
                    }
                    
                    &nbsp; <i onClick={() => setIsModalOpen(true)} className="fa fa-chevron-down gamescore__score__commentary" />
                </div>
            </div>
        </>
    } else if (game.SportId == 4 || game.SportId == 12 || game.SportId == 7 || game.SportId == 33) {
        content = <><div className="gamescore__flex-container">
            <div className="gamescore__flex-item teamname">
                {game.HomeTeam}
            </div>
            <div className="gamescore__flex-item vs">
                { gameType == 'game' ? (game.Scoreboard?.score ? game.Scoreboard.score : "-- : --") : <div className="gamescore__container__date"><span>{`${new Date(game.Date).toLocaleTimeString()}`}</span><span>{`${new Date(game.Date).toLocaleDateString()}`}</span></div>}
            </div>
            <div className="gamescore__flex-item teamname">
                {game.AwayTeam}
            </div>
        </div>
            <div className="gamescore__flex-container">
                <div className="gamescore__flex-item score">
                    <i onClick={() => setIsModalOpen(true)} className="fa fa-chevron-down gamescore__score__commentary" />
                </div>
            </div>
        </>
    } else if (game.SportId == 5 || game.SportId == 18 || game.SportId == 56 || game.SportId == 44) {
        content = <><div className="gamescore__flex-container">
            <div className="gamescore__flex-item teamname">
                {game.HomeTeam}
            </div>
            <div className="gamescore__flex-item vs">
                {gameType == 'game' ? `${game.Scoreboard?.points[0] ? game.Scoreboard.points[0] : '--'} : ${game.Scoreboard?.points[1] ? game.Scoreboard.points[1] : '--'}` : <div className="gamescore__container__date"><span>{`${new Date(game.Date).toLocaleTimeString()}`}</span><span>{`${new Date(game.Date).toLocaleDateString()}`}</span></div>}
            </div>
            <div className="gamescore__flex-item teamname">
                {game.AwayTeam}
            </div>
        </div>
            <div className="gamescore__flex-container">
                <div className="gamescore__flex-item score">
                    <i onClick={() => setIsModalOpen(true)} className="fa fa-chevron-down gamescore__score__commentary" />
                </div>
            </div>
        </>
    } else {
        content = null
    }

    // var events;
    // if(results){
    //     var foundResult = results.find(res => res.Id == game.Id);
    //     if(foundResult){
    //         events = foundResult.events;
    //     } else {
    //         events = []
    //     }
    // } else {
    //     events = []
    // }


    return <div className="gamescore__container">
        {isModalOpen && <Modal
            show={isModalOpen}
            onCancel={() => setIsModalOpen(false)}
            header={"Commentary"}
            footer={<></>}
        >
            <div className="fixed-modal-area">

                {/* {commentary} */}

                {
                    game.Scoreboard?.messages && !Array.isArray(game.Scoreboard.messages) ? Object.keys(game.Scoreboard.messages).reverse().map(id => {
                        return <div key={id} className="fixed-modal-item">
                            {game.Scoreboard.messages[id].content}
                        </div>
                    })
                        :
                        (events.length > 0 ? events.map(event => {
                            return <div key={event.id} className="fixed-modal-item">
                                {event.text}
                            </div>
                        })
                            :
                            (
                                isLoading 
                                ?
                                <span className="noResults">Loading...</span>
                                :
                                <span className="noResults">No Results were found.</span>
                            )
                        )
                }
            </div>
        </Modal>
        }
        {content}
    </div>
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token,
    results:state.results.results
})

const mapDispatchToProps = dispatch => ({
    putResults: result => dispatch(putResults(result))
})
  
  
export default connect(mapStateToProps, mapDispatchToProps)(GameScore);
