import ActionTypes from './betslip.types';
import { calculateTotalReturn } from './betslip.utils';

const INITIAL_STATE = {
    bets: [],
    updateBet: true,
    updatedGame: true,
    betType:'single',
    totalStake:0,
    singleTotalReturn:0,
    multiTotalReturn:0,
    systemTotalReturn:0,
    errorMessage:null,
    errorValue:null,
    useBonus:false
}

export const betslipReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ActionTypes.SET_UPDATE_BET:
          return {
                ...state,
                updateBet: action.value
            };
        case ActionTypes.SET_BET_TYPE:
            return {
                    ...state,
                    betType: action.value
                };
            case ActionTypes.CLEAR_BETS:
                return {
                      ...state,
                      bets: [],
                      betType:'single',
                      totalStake:0,
                      singleTotalReturn:0,
                      multiTotalReturn:0,
                      systemTotalReturn:0,
                      errorMessage:null
                  };
        case ActionTypes.SET_TOTAL_STAKE:
            return {
                    ...state,
                    totalStake: Number(action.value) 
                };
        case ActionTypes.SET_ERROR_MESSAGE:
            return {
                    ...state,
                    errorMessage: action.errorMessage 
                };
        case ActionTypes.SET_ERROR_VALUE:
            return {
                    ...state,
                    errorValue: action.value
                };
        case ActionTypes.SET_USE_BONUS:
            return {
                    ...state,
                    useBonus:action.value
                };
        case ActionTypes.PUT_BET:
            return {
                    ...state,
                    bets: [action.value, ...state.bets]
                };
        case ActionTypes.REPLACE_BET:
            var betsCopy = state.bets
            betsCopy[action.oldBetIndex] = action.newBet;
            return {
                ...state,
                bets: betsCopy
            }
        case ActionTypes.UPDATE_BET_ODD:
            var betsOddsCopy = [...state.bets];
            var indexOdds = betsOddsCopy.findIndex(n => n._id === action.betId);
            betsOddsCopy[indexOdds].odds = action.value;
            return {
                    ...state,
                    bets: betsOddsCopy
                };
        case ActionTypes.UPDATE_BET_STAKE:
            var betsStakeCopy = state.bets;
            var indexStake = betsStakeCopy.findIndex(n => n._id === action.betId);
            betsStakeCopy[indexStake].stake = Number(action.value);
            return {
                    ...state,
                    bets: betsStakeCopy
                };
        case ActionTypes.DELETE_BET:
            var betsCopy = state.bets;
            const newBets = betsCopy.filter(b => b._id != action.betId)
            return {
                    ...state,
                    bets: newBets
                };
        case ActionTypes.CALCULATE_TOTAL_RETURN:
            return calculateTotalReturn(state);
    
        default:
            return state;
      }
}