import { createSelector } from 'reselect';

const selectBetslip = (state) => state.betslip;

export const selectBets = createSelector(
    [selectBetslip],
    (betslip) => betslip.bets
);

export const selectUpdateBet = createSelector(
    [selectBetslip],
    (betslip) => betslip.updateBet
);

export const selectBetType = createSelector(
    [selectBetslip],
    (betslip) => betslip.betType
);

export const selectTotalStake = createSelector(
    [selectBetslip],
    (betslip) => betslip.totalStake
);

export const selectErrorMessage = createSelector(
    [selectBetslip],
    (betslip) => betslip.errorMessage
);

export const selectErrorValue = createSelector(
    [selectBetslip],
    (betslip) => betslip.errorValue
);

export const selectSingleTotalReturn = createSelector(
    [selectBetslip],
    (betslip) => betslip.singleTotalReturn
);

export const selectMultiTotalReturn = createSelector(
    [selectBetslip],
    (betslip) => betslip.multiTotalReturn
);

export const selectSystemTotalReturn = createSelector(
    [selectBetslip],
    (betslip) => betslip.systemTotalReturn
);

export const selectUseBonus = createSelector(
    [selectBetslip],
    (betslip) => betslip.useBonus
);