import * as React from "react";
import { connect } from "react-redux";

import { setOddType } from "../../redux/oddType/oddType.actions";
import { setPageType } from '../../redux/page/page.actions';

import MainLayout from '../../layouts/main-layout.component';
import SportsList from "../../components/sportsList/sportsList.component";
import QuizViewList from "../../components/quiz-view-list/QuizViewList";
//import BetSlip from "../../components/betslip/BetSlip";

import { useSocket } from "../../socket/socket";



function QuizOverview({ user, token, pregames, cricketgames, oddType, setOddType, setPageType }) {
    const [sortedGames] = React.useState(['Cricket', 
    'Football', 'Tennis', 'Baseball', 'Basketball', 'Volleyball','Table Tennis',
    'Badminton','American Football', 'Futsal', 'Handball', 'Ice Hockey', 'Snooker',
    'Darts', 'Rugby Union', 'Rugby League', 'Boxing', 'Aussie Rules', 'Formula 1', 
    'Golf', 'Motorbikes', 'Cycling', 'Alpine Skiing', 'Biathlon', 'E-Sports'])
    const [ sportsData, setSportsData ] = React.useState([]);
    const [formattedData, setFormattedData] = React.useState(null);
    const [ notFound, setNotFound ] = React.useState(true);
    const [property, setProperty] = React.useState("Cricket");
    const [menuOpen, setMenuOpen] = React.useState(false);
    

    const handletabChange = (event) => {
        var els = document.getElementsByClassName("gamepage__tab__item");
        Array.prototype.forEach.call(els, function(el) {
            // Do stuff here
            el.classList.remove("active")
        });
        event.target.classList.add("active");

        setProperty(event.target.dataset.filter);
        //console.log(property)
        
    }
    
    useSocket();

    React.useLayoutEffect(() => {
        setPageType('sports');
    },[]);

    React.useEffect(() => {
        if(user){
            if((pregames != null || pregames != undefined) && (cricketgames != null || cricketgames != undefined)){
                //console.log(data.results);
                let copyGames = {};
                pregames.forEach(result => {
                    copyGames[result.SportName] ? 
                        copyGames[result.SportName] = [...copyGames[result.SportName], result ] 
                        : 
                        copyGames[result.SportName] = [result];
                })
                //console.log(copyGames);
                copyGames['Cricket'] = cricketgames;
                setNotFound(false)
                setFormattedData(copyGames);
                setSportsData(pregames);
            } else{
                setSportsData([]);
                setNotFound(true);
            }
        }
    },[user, token, pregames, cricketgames])

    React.useEffect(() => {
        if(user){
            if(cricketgames != null || cricketgames != undefined){
                //console.log(data.results);
                // let copyGames = {};
                // games.forEach(result => {
                //     copyGames[result.SportName] ? 
                //         copyGames[result.SportName] = [...copyGames[result.SportName], result ] 
                //         : 
                //         copyGames[result.SportName] = [result];
                // })
                // //console.log(copyGames);
                setNotFound(false)
                // setFormattedData(copyGames);
                // setSportsData(games);
            } else{
                //setSportsData([]);
                setNotFound(true);
            }
        }
    },[user, token, cricketgames])

    var tabs;
        
    tabs = <div className="gamepage__tab__container quiz-tab">
        {
            menuOpen ?
            sortedGames.map((name, i) => (
                    <div key={name} onClick={handletabChange} className={`gamepage__tab__item  ${name == property ? "active" : "" }`} data-filter={name}>
                        {name}
                    </div>
                
            ))
            :
            sortedGames.slice(0,5).map((name, i) => (
                <div key={name} onClick={handletabChange} className={`gamepage__tab__item  ${name == property ? "active" : "" }`} data-filter={name}>
                    {name}
                </div>
            
            ))
        }
        <div className="gamepage__tab__container__opener" onClick={() => setMenuOpen(!menuOpen)}>
            {
                menuOpen ?
                <p>Show less  <i className="fa fa-chevron-up" /> </p>
                :
                <p>Show more  <i className="fa fa-chevron-down" /> </p>
            }
            
            
        </div>
    </div>


  return (
      	<MainLayout>
            <div className="resultsContainer">
                {tabs}
                {
                    notFound && <span className="noResults">Fetching Results Please Wait...</span>
                }

                {/* {
                    user && !notFound && cricketgames && <>
                    
                        <React.Fragment>
                            <h1 className="game-page__title">Cricket</h1>
                            {
                                <SportsList 
                                    results={cricketgames}
                                    gameType={'precricket'}
                                />    
                            }
                        </React.Fragment>
                    
                    </>     
                } */}

                {
                    user && !notFound && pregames && cricketgames && formattedData && <>
                    {
                        // property == 'Cricket' ?
                        // <React.Fragment>
                        //     {
                        //         <QuizViewList 
                        //             results={cricketgames}
                        //             gameType={property}
                        //         />    
                        //     }
                        // </React.Fragment>
                        // :
                        // <React.Fragment>
                        //     {
                        //         <QuizViewList 
                        //             results={[]}
                        //             gameType={property}
                        //         />    
                        //     }
                        // </React.Fragment>
                        <React.Fragment>
                            {
                                <QuizViewList 
                                     results={formattedData[property] ? formattedData[property] : []}
                                     gameType={property}
                                 />    
                             }
                         </React.Fragment>
                    }
                    </>     
                }
                {/* {bets.length > 0 && <BetSlip />} */}
            </div>
      	</MainLayout>
  );
}

const mapStateToProps = state => ({
    user: state.user.user,
    token: state.user.token,
    pregames: state.pregame.games,
    cricketgames: state.pregame.cricketData,
    oddType: state.oddType.oddType,
    bets: state.betslip.bets
})

const mapDispatchToProps = dispatch => ({
    setOddType: oddType => dispatch(setOddType(oddType)),
    setPageType: (pageType) => dispatch(setPageType(pageType))
})
 

export default connect(mapStateToProps, mapDispatchToProps)(QuizOverview);